import { Component, OnInit } from '@angular/core';
import { AdminDashboardService } from '../admin-dashboard.service';
import { DialogService } from '../../../core/services/dialog.service';
import { MatTableDataSource } from '@angular/material';
import { CustomFormComponent } from '../../../core/components/custom-form/custom-form.component';
import { createClientForm } from './create-client-form';
import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'app-manage-logins',
    templateUrl: './manage-logins.component.html',
    styleUrls: ['./manage-logins.component.scss']
})
export class ManageLoginsComponent implements OnInit {

    dataSource: any;
    clientLogin = {};

    displayedColumns: string[] = ['firstName', 'companyOrAgencyName', 'email', 'actions'];
    
    constructor(
        public apiSerive: AdminDashboardService,
        public dialog: DialogService,
        public toaster: ToastrService
    ) { }

    ngOnInit() {
        this.getClientsList();
    }

    getClientsList() {
        const loader = this.dialog.createLoader();
        this.apiSerive.getClientsList()
            .then((response: any) => {
                this.dataSource = new MatTableDataSource(response.map(e => {e.action  = true; return e}));
                loader.close();
            }, () => {
                this.dataSource = [];
                loader.close();
            })
    }

    applyFilter(filterValue: string) {
        this.dataSource.filter = filterValue.trim().toLowerCase();
    }

    setClientLogin(client) {
        const form = {...createClientForm};
        if (client) {
            delete form.email;
        }
        const dialogForm = this.dialog.createForm({
            type: 'custom',
            title: 'Create Client Login',
            component: CustomFormComponent,
            panelClass: 'custom',
            dataProperties: {
              inputs : {
                lookup: form,
                model: client || {}
            },
            outputs : {
                submit: (event) => {
                    const loader = this.dialog.createLoader();
                    if (client) {
                        const payload = {...client, ...event};
                        this.apiSerive.updateClientLogin(payload).then((res) => {
                            this.toaster.success(`"${payload.email}" client updated successfully`);
                            loader.close();
                            dialogForm.close();
                            this.getClientsList();
                        }, (err) => {
                            loader.close();
                            this.dialog.createAlert('Please try again later');
                        });
                    } else {
                        event.active = true;
                        this.apiSerive.createClientLogin(event).then((res) => {
                            this.toaster.success(`"${event.email}" client created successfully`);
                            loader.close();
                            dialogForm.close();
                            this.getClientsList();
                        }, (err) => {
                            loader.close();
                            this.dialog.createAlert('Please try again later');
                        });
                    }
                }
            }
            }
        });
    }

    deleteClientLogin(client) {
        this.dialog.createConfirm(`Are you sure? You want to delete ${client.email} client`, () => {
            const loader = this.dialog.createLoader();
            this.apiSerive.deleteClient(client.uuid).then(() => {
                loader.close();
                this.toaster.success(`"${client.email}" client deleted successfully`);
                this.getClientsList();
            }, () => {
                loader.close();
                this.dialog.createAlert('Something went wrong. Please try again later.');
            })
        })
    }

}
