import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { AuthService } from './auth.service';
import { DialogService } from './dialog.service';
import { HttpService } from './http.service';

@Injectable({
    providedIn: 'root'
})
export class GuardService {

    constructor(
        public authService: AuthService,
        public dialog: DialogService,
        public http: HttpService,
        private router: Router
    ) { }

    /**
       * Delegate for canActivate() that changes the signatures to facilitate unit testing.
       * @param route
       * @param state
       * @returns {boolean}
       * @private
       */
    _canActivate(route: any, state: any) {
        if (route.routeConfig.path === 'account-verification/:id') {
            const loader = this.dialog.createLoader();
            this.http.put('api/v1/user/account-status', {userId: route.params.id, active: true})
                .then((res) => {
                    loader.close();
                    this.router.navigate(['/register/success'], {});
                    return false;
                }, (res) => {
                    loader.close();
                    this.router.navigate(['/'], {});
                    return false;
                })
        } else {
            if (this.authService.isAuthenticated()) {
                return true;
            }
    
            // Store the attempted URL and query params for redirecting
            this.authService.redirectUrl = state.url;
            this.authService.queryParams = route.queryParams;
    
            // Mark the redirect URL
            sessionStorage.setItem('redirectUrl', state.url);
    
            // Navigate to the login page with extras
            this.router.navigate(['/login'], { queryParams: route.queryParams });
            return false;
        }
    }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        return this._canActivate(route, state);
    }

    canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        return this.canActivate(route, state);
    }
}
