import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { ApiService } from '../../services/api.service';
import { DialogService } from '../../services/dialog.service';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';

@Component({
    selector: 'app-reset-password',
    templateUrl: './reset-password.component.html',
    styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {

    resetForm: FormGroup;

    constructor(public commonApi: ApiService, public dialog: DialogService, public toaster: ToastrService, public router: Router) { }

    ngOnInit() {
        this.resetForm = new FormGroup({
            email: new FormControl('', [Validators.required, Validators.email])
            // password: new FormControl('', [Validators.required]),
            // reenterPassword: new FormControl('', [Validators.required, this.checkPasswordMatch.bind(this)])
        });
    }

    checkPasswordMatch(control: AbstractControl) {
        if (this.resetForm) {
            const password = this.resetForm.get('password').value;
            return password !== control.value ? { notMatched: { value: control.value } } : null;
        }
        return null;
    }

    hasError(controlName: string, errorName: string) {
        return this.resetForm.controls[controlName].hasError(errorName);
    }

    resetPassword(resetDetails) {
        if (this.resetForm.valid) {
            const loader = this.dialog.createLoader();
            this.commonApi.forgotPassword(this.resetForm.value).then(() => {
                loader.close();
                this.router.navigate(['/reset-password/success']);
            }, () => {
                loader.close();
                this.dialog.createAlert('Unable to reset your password. Try later!');
            })
        }
    }
}
