import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { HttpService } from '../../services/http.service';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

    isAuthenticated: boolean;
    userProfile: any;
    hideTabs = false;
    showMenu = false;
    constructor(public auth: AuthService,
        public http: HttpService,
        public router: Router) { }

    ngOnInit() {
        this.isAuthenticated = this.auth.isAuthenticated();
        if (this.isAuthenticated) {
            if (!this.auth._user || !this.auth._user.firstName) {
                this.http.get(`api/v1/user/details`, {}, { auth: true })
                    .then((res) => {
                        if (!res.data) {
                            this.signout();
                            return;
                        }
                        this.auth.setUser(res.data);
                        this.userProfile = this.auth._user;
                        this.userProfile['name'] = res.data.firstName + ' ' + (res.data.lastName || '');
                        this.hideTabs = this.auth._user && (this.auth._user.role === 'superAdmin' || this.auth._user.role === 'client');
                    }, () => {
                        this.signout();
                    })
            } else {
                this.userProfile = this.auth._user;
                this.userProfile['name'] = this.auth._user.firstName + ' ' + (this.auth._user.lastName || '');
                this.hideTabs = this.auth._user && (this.auth._user.role === 'superAdmin' || this.auth._user.role === 'client');
            }
        }
    }

    signout($event?: any) {
        if ($event) $event.preventDefault();
        this.auth.signout();
        this.isAuthenticated = false;
        this.hideTabs = false;
        if (this.router.url.includes('dashboard')) {
            this.router.navigate(['/']);
        }

    }

    navigateToLogin($event?: any) {
        if ($event) $event.preventDefault();
        this.router.navigate(['/login']);
    }

}
