import { Component, OnInit } from '@angular/core';
import { DialogService } from '../../core/services/dialog.service';
import { AuthService } from '../../core/services/auth.service';
import { JobsService } from '../jobs.service';
import { ToastrService } from 'ngx-toastr';
import { TalentDetailsComponent } from '../../talent-details/talent-details.component';

@Component({
    selector: 'app-applied-jobs',
    templateUrl: './applied-jobs.component.html',
    styleUrls: ['./applied-jobs.component.scss']
})
export class AppliedJobsComponent implements OnInit {
    data: any;
    dismiss: any;
    appliedCandidates: any[];
    showSendNotification = false;
    showBookTalent = false;

    changeInApplicantSelect = {};
    get selectedProfiles() {
        return (this.appliedCandidates || []).filter(item => item.selected && !item.notification);
    }

    constructor(public dialog: DialogService, public auth: AuthService, public apiService: JobsService, public toaster: ToastrService) { }

    ngOnInit() {
        this.showSendNotification = this.auth._user.role === 'superAdmin';
        this.showBookTalent = this.auth._user.role === 'client';

        this.auth.userUpdated.subscribe(() => {
            this.showSendNotification = this.auth._user.role === 'superAdmin';
            this.showBookTalent = this.auth._user.role === 'client';
        });

        const loader = this.dialog.createLoader();
        this.apiService.getAppliedUsers({ jobId: this.data.jobId, roleId: this.data.roleId }).then((response: any) => {
            this.appliedCandidates = response;
            loader.close();
        }, () => {
            loader.close();
        });

    }

    changeApplicantSelect(item) {
        item.selected = !item.selected;
        if (this.changeInApplicantSelect[item.userId]) {
            delete this.changeInApplicantSelect[item.userId];
        } else {
            this.changeInApplicantSelect[item.userId] = {
                id: item.appliedId,
                selected: item.selected
            };
        }
    }

    onDialogClose() {
        this.dismiss();

        // const payload = Object.values(this.changeInApplicantSelect);
        // if (payload.length) {
        //     const loader = this.dialog.createLoader();
        //     this.apiService.selectApplicants(payload).then((res) => {
        //         this.toaster.success('Applicants selection updated successfully');
        //         loader.close();
        //         this.dismiss();
        //     }, () => {
        //         loader.close();
        //         this.dismiss();
        //         this.dialog.createAlert('Unable to select applicants. Please try later');
        //     });
        // } else {
        //     this.dismiss();
        // }
    }

    sendNotification() {
        const payload = Object.values(this.changeInApplicantSelect);
        if (payload.length) {
            const loader = this.dialog.createLoader();
            this.apiService.selectApplicants(payload).then((res) => {
                const n_payload = this.selectedProfiles.map(n => ({
                    id: n.appliedId,
                    notification: true,
                    userId: n.userId,
                    jobId: this.data.jobId
                }));
                this.apiService.sendNotifications(n_payload).then(() => {
                    this.changeInApplicantSelect = {};
                    this.selectedProfiles.forEach(e => {
                        e.notification = true;
                    });
                    this.toaster.success('Notification sent successfully');
                    loader.close();
                }, () => {
                    loader.close();
                });
            }, () => {
                loader.close();
                this.dismiss();
                this.dialog.createAlert('Unable to select applicants. Please try later');
            });
        } else {
            const loader = this.dialog.createLoader();
            const n_payload = this.selectedProfiles.map(n => ({
                id: n.appliedId,
                notification: true,
                userId: n.userId,
                jobId: this.data.jobId
            }));
            this.apiService.sendNotifications(n_payload).then(() => {
                this.changeInApplicantSelect = {};
                this.selectedProfiles.forEach(e => {
                    e.notification = true;
                });
                this.toaster.success('Notification sent successfully');
                loader.close();
            }, () => {
                loader.close();
            });
        }
    }


    showDetails(event, talent) {
        if (event) {
            event.preventDefault();
            event.stopPropagation();
        }

        const dialogForm = this.dialog.create({
            disableClose: true,
            data: {
                type: 'custom',
                title: 'View Profile',
                component: TalentDetailsComponent,
                panelClass: 'custom',
                dataProperties: {
                    inputs: {
                        data: talent
                    }
                }
            }
        });
    }

    bookTalent(){
        const payload = Object.values(this.changeInApplicantSelect);
        if (payload.length) {
            const loader = this.dialog.createLoader();
            this.apiService.selectApplicants(payload).then((res) => {
                this.toaster.success('Applicants selection updated successfully');
                loader.close();
            }, () => {
                loader.close();
                this.dialog.createAlert('Unable to select applicants. Please try later');
            });
        } 
    }

}
