import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { UserDashboardService } from '../dashboard/user-dashboard/user-dashboard.service';
// , FeaturesDetailsLookup
import { PersonalDetailsLookup, AppearanceDetailsLookup, SportsDetailsLookup, ProfileDetails, ArtsDetailsLookup } from '../dashboard/user-dashboard/profile/profile';
import { ActivatedRoute } from '@angular/router';
import { DialogService } from '../core/services/dialog.service';
import { AuthService } from '../core/services/auth.service';

import { jsPDF } from 'jspdf';
import html2canvas from 'html2canvas';

@Component({
    selector: 'app-talent-details',
    templateUrl: './talent-details.component.html',
    styleUrls: ['./talent-details.component.scss'],
    providers: [UserDashboardService]
})
export class TalentDetailsComponent implements OnInit {
    dismiss: any;
    zeroState = {
        message: 'Talent Details component is under construction'
    }
    @Input() data: any;
    profileData: any;
    profileCategories: any;
    profilePersonalData: any;
    profilePhysicalData: any;
    profileArtsData: any;
    profileSportsData: any;
    profileCombatTrainingData: any;
    profileOthersData: any;

    profileImages = [];

    active_apperance_sports = false;
    hideContent = true;
    // lookups
    personalDetailsLookup = {};
    apperanceDetailsLookup = AppearanceDetailsLookup;
    sportsDetailsLookup = SportsDetailsLookup;
    artsDetailsLookup = ArtsDetailsLookup;
    // featuresDetailsLookup = FeaturesDetailsLookup;
    isAdmin = false;

    constructor(public dialog: DialogService, public apiService: UserDashboardService, public auth: AuthService) { }

    async ngOnInit() {
        this.isAdmin = this.auth._user && (this.auth._user.role.includes('superAdmin'));

        this.personalDetailsLookup = { ...PersonalDetailsLookup };

        if (!this.auth._user || this.auth._user.role !== 'superAdmin') {
            delete this.personalDetailsLookup['phoneNumber'];
            delete this.personalDetailsLookup['email'];
        }

        this.auth.userUpdated.subscribe(() => {
            if (!this.auth._user || this.auth._user.role !== 'superAdmin') {
                delete this.personalDetailsLookup['phoneNumber'];
                delete this.personalDetailsLookup['email'];
            }
        });

        Promise.all([this.apiService.getPortfolio(this.data.userId)]).then((res) => {
            this.setPortfolio(res[0].data || {});
        }, (error) => {
            console.log('error')
        });

        const loader = this.dialog.createLoader();
        if (typeof this.data === 'string') {
            console.log("check");
        } else {
            this.hideContent = false;
            loader.close();

            this.profileData = new ProfileDetails(this.data);

            if (!this.auth._user || this.auth._user.role !== 'superAdmin') {
                delete this.profileData.personalDetails.phoneNumber
                delete this.profileData.personalDetails.email
                delete this.profileData.personalDetails.name
            }

            this.profileCategories = this.data.categories ? JSON.parse(this.data.categories) : [];

            if (this.profileCategories.length) {
                this.profileCategories.map(category => {
                    if (category == "Actors" || category == "Emcees" || category == "Models" || category == "Entertainers & Performers") {
                        this.active_apperance_sports = true;
                    }
                });
            }

            this.profilePersonalData = {
                date_of_birth: this.data.dateOfBirth,
                age: this.data.age,
                gender: this.data.gender,
                ethnicity: this.data.ethnicity,
                nationality: this.data.nationality,
                speaks: this.data.languages ? JSON.parse(this.data.languages).join(", ") : [],
            };

            this.profilePhysicalData = {
                ethnic_look: this.data.ethnicLook,
                hair_type: this.data.hairType,
                hair_color: this.data.hairColor,
                eye_color: this.data.eyeColor,
                height: this.data.height ? this.data.height + " CM" : null,
                shoe_size: this.data.shoeSize ? this.data.shoeSize + " EU" : null,
                shirt_size: this.data.tshirtSize,
                pant_size: this.data.pantSize,
                jacket_size: this.data.jacketSize,
                dress_size: this.data.dressSize,
                chest: this.data.chest ? this.data.chest + " CM" : null,
                waist: this.data.waist ? this.data.waist + " CM" : null,
                hips: this.data.hips ? this.data.hips + " CM" : null,
                tattoos: this.data.tattoos,
                piercings: this.data.piercings,
                facial_hair: this.data.facialHair,
            };

            this.profileArtsData = {
                singing: this.data.singing ? JSON.parse(this.data.singing).join(", ") : [],
                instruments: this.data.instruments ? JSON.parse(this.data.instruments).join(", ") : [],
                dance: this.data.dance ? JSON.parse(this.data.dance).join(", ") : [],
                vocal_range: this.data.vocalRange ? JSON.parse(this.data.vocalRange).join(", ") : [],
            }

            this.profileSportsData = {
                water_sports: this.data.waterSports ? JSON.parse(this.data.waterSports).join(", ") : [],
                winter_sports: this.data.winterSports ? JSON.parse(this.data.winterSports).join(", ") : [],
                gymnastics_and_fitness: this.data.gymnastics ? JSON.parse(this.data.gymnastics).join(", ") : [],
                team_sports: this.data.teamSports ? JSON.parse(this.data.teamSports).join(", ") : [],
                misc_sports: this.data.miscSports ? JSON.parse(this.data.miscSports).join(", ") : [],
                swimming: this.data.swimming ? JSON.parse(this.data.swimming).join(", ") : [],
                track_and_field: this.data.trackAndField ? JSON.parse(this.data.trackAndField).join(", ") : [],
                cycling: this.data.cycling ? JSON.parse(this.data.cycling).join(", ") : [],
                martial_arts: this.data.martialArts ? JSON.parse(this.data.martialArts).join(", ") : [],
                martial_arts_weapons_training: this.data.martialArtsWeaponsTraining ? JSON.parse(this.data.martialArtsWeaponsTraining).join(", ") : [],
                general_weapons_training: this.data.generalWeaponsTraining ? JSON.parse(this.data.generalWeaponsTraining).join(", ") : [],
            }

            this.profileCombatTrainingData = {
                martial_arts: this.data.martialArts ? JSON.parse(this.data.martialArts).join(", ") : [],
                martial_arts_weapons_training: this.data.martialArtsWeaponsTraining ? JSON.parse(this.data.martialArtsWeaponsTraining).join(", ") : [],
                general_weapons_training: this.data.generalWeaponsTraining ? JSON.parse(this.data.generalWeaponsTraining).join(", ") : [],
            }
        }
    }

    setPortfolio(data) {
        this.profileImages = data.images ? (JSON.parse(data.images) || []).map(e => ({ url: e })) : [];
    }

    returnZero() {
        return 0
    }


    downloadPDF() {
        var profilePhoto = this.profileData.personalDetails.image;
        var name = this.profileData.personalDetails.name;
        var age = this.profileData.age

        var profileCategoriesHtml = this.profileCategories.map(profileCategory => {
            return `<div style="margin-bottom:10px;font-size:8px;background:rgba(226, 226, 226, 0.34);border-radius:15px;display:inline-block;line-height:15px;text-align:center;padding:0 3px 3px 6px;">${profileCategory}</div>`
        })

        var physicalAppearanceHtml = '';
        var paIndex = 0;

        for (const key in this.profilePhysicalData) {
            if (paIndex === 0) {
                physicalAppearanceHtml += `<tr style="border-top:none">`
            }

            if (paIndex != 0 && paIndex % 6 == 0) {
                physicalAppearanceHtml += `</tr><tr style="border-top:none">`
            }

            physicalAppearanceHtml +=
                `<td style="border-top:none;width: 16.67%;padding-top: 0.5rem;">
                    <div style="font-size:7.5px;color:#5a5a5a;margin-bottom:5px;">${(key.split("_").join(" ")).replace(/\b\w/g, function (l) { return l.toUpperCase() })}</div>
                    <div style="font-size:7.5px;">${this.profilePhysicalData[key]}</div>
                </td>`;

            if (paIndex == (Object.keys(this.profilePhysicalData).length - 1)) {
                physicalAppearanceHtml += `</tr>`
            }

            paIndex++
        }

        var imagesHtml = ''
        this.profileImages.slice(0, 5).forEach(image => {
            imagesHtml +=
                `<td style="border-top:none;padding: 1rem 0.1rem 0 0.1rem;">
                    <div style="width:116px;height:180px;position:relative;overflow:hidden;">
                        <img src="${image.url}" style="width:auto;height:100%;position:absolute;top:50%;left:50%;transform:translate(-50%,-50%)"/>
                    </div>
                </td>`;
        })

        var data =
            `
        <!DOCTYPE html>
        <html>
            <head>
                <meta http-equiv="Content-Type" content="text/html; charset=UTF-8">
                <meta name="viewport" content="width=device-width, initial-scale=1">
                <link rel="preconnect" href="https://fonts.gstatic.com">
                <link href="https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap" rel="stylesheet">
            </head>
            <body style="font-size:12px;margin-bottom:0;padding-bottom:0;">
                <table class="table" style="font-size:10px;border-bottom:1px solid #000;border-top:none;width:600px;margin-bottom:0;">
                    <tbody style="width: 100%;">
                        <tr style="width: 100%;">
                            <td style="border-top:none;width:140px;padding:0;padding-bottom:1rem;">
                                <img src="${profilePhoto}" width="140" style="">
                            </td>
                            <td style="border-top:none;padding-bottom:0;padding-top: 0.5rem;">
                                <div style="white-space:nowrap;margin-bottom:14px;">
                                    <span style="font-size:14px;font-weight:bold;font-family:'Ubuntu',sans-serif;background: #8c1f3d;color:#fff;padding: 5px 7px 7px 7px;">${name} (${age} yrs old)</span>
                                </div>
                                <div style="margin-bottom:5px;font-family: 'Ubuntu', sans-serif;width:100%;">
                                    ${profileCategoriesHtml.join(" ")}
                                </div>
                                <table class="table table-sm" style="width:100%;font-size:10px;font-family: 'Ubuntu', sans-serif;margin-bottom:0.25rem;">
                                    <tbody>
                                        <tr>
                                            <td colspan="6" style="border-top:none;font-weight:bold;">Physical Appearance</td>
                                        </tr>
                                        ${physicalAppearanceHtml}
                                    </tbody>
                                </table>
                            </td>
                        </tr>
                    </tbody>
                </table>

                <table class="table" style="font-size:10px;border-top:none;width:600px;margin-bottom:0;padding-bottom:0;">
                    <tbody style="width: 100%;">
                        <tr style="width: 100%;">
                            ${imagesHtml}
                        </tr>
                    </tbody>
                </table>
            </body>
        </html>
        `

        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0');
        var yyyy = today.getFullYear();
        var todayString = mm + '-' + dd + '-' + yyyy;
        var filename = "talent_"+this.profileData.userId + "_" + todayString;

        let doc = new jsPDF('landscape', 'px', 'a4');
        doc.html(data, {
            callback: function (doc) {
                doc.save(filename);
            },
            x: 10,
            y: 10,
            margin: [10, 10, 10, 10]
        });
    }
}
