import { Component, OnInit, Input } from '@angular/core';
import { JobsService } from './jobs.service';
import { AuthService } from '../core/services/auth.service';
import { ApiService } from '../core/services/api.service';
import { DialogService } from '../core/services/dialog.service';

@Component({
    selector: 'app-jobs',
    templateUrl: './jobs.component.html',
    styleUrls: ['./jobs.component.scss']
})
export class JobsComponent implements OnInit {

    zeroState = {
        message: 'Jobs component is under construction'
    }

    @Input() onlyJobs = false;
    loader = true;
    jobs: any[];
    selectedCategories = [];
    selectedlocation = "";
    selectedGender = "";
    jobCategories: any[];
    locationCategory: any[];
    genderCategory = [
        { type: 'gender', id: 'MALE', displayName: 'Male' },
        { type: 'gender', id: 'FEMALE', displayName: 'Female' }
    ];
    jobPostedSelected: any;

    constructor(public apiService: JobsService, public auth: AuthService, public commonService: ApiService, public dialog: DialogService) { }

    ngOnInit() {

        this.getJobs();
        this.auth.userUpdated.subscribe(() => {
            this.getJobs();
        });

        this.commonService.getCategories().then((res: any) => {
            this.jobCategories = res.data.map(e => ({ type: "category", id: e.category, displayName: e.category }));
        });

        this.commonService.getCountries().then((res: any) => {
            this.locationCategory = res.data.map(e => ({ type: "location", id: e.name, displayName: e.name }));
        });
    }

    getJobs() {
        const loader = this.dialog.createLoader();
        if (this.onlyJobs && this.auth._user) {
            if (this.auth._user.role === 'client') {
                this.apiService.getClientJobs().then((data: any) => {
                    loader.close();
                    this.jobs = data;
                    this.loader = false;
                }, () => {
                    loader.close();
                    this.loader = false;
                });
            } else if (this.auth._user.role === 'user') {
                this.apiService.getUserJobs().then((data: any) => {
                    loader.close();
                    this.jobs = data;
                    this.loader = false;
                }, () => {
                    loader.close();
                    this.loader = false;
                });
            }
        } else {
            this.apiService.getAllJobs().then((data: any) => {
                loader.close();
                this.jobs = data;
                this.loader = false;
            }, () => {
                loader.close();
                this.loader = false;
            });
        }
    }

    jobCategorySelectionChange($event) {
        const value = $event.source.value;
        if ($event.checked) {
            this.selectedCategories.push(value);
            value.checked = true;
        } else {
            const index = this.selectedCategories.findIndex((item) => item.id === value.id);
            this.selectedCategories.splice(index, 1);
        }
        this.filterJobs();
    }

    CategoyChange($event) {
        this.selectedCategories = this.selectedCategories.filter(item => item.type !== 'recentJobPostedOptions');
        this.selectedCategories = $event.value;
        // this.filterJobs();
    }

    LocationChange($event) {
        this.selectedlocation = $event.value.id;
        // this.filterJobs();
    }

    GenderChange($event) {
        this.selectedGender = $event.value.id;
        // this.filterJobs();
    }

    clearFilter(event) {
        if (this.jobPostedSelected && event.id === this.jobPostedSelected.id) {
            this.jobPostedSelected = null;
        }
        const index = this.selectedCategories.findIndex((item) => item.id === event.id);
        this.selectedCategories.splice(index, 1);
        this.jobCategories.forEach((item) => {
            if (item.id === event.id) {
                item['checked'] = false;
            }
        });
        this.filterJobs();
    }

    clearAllFilters() {
        this.selectedCategories = [];
        this.jobPostedSelected = null;
        this.jobCategories.forEach(item => item['checked'] = false);
        this.jobPostedSelected = undefined;
        this.filterJobs();
    }

    afterJobUpdate(event, i) {
        if (event === 'deleted') {
            this.jobs.splice(i, 1);
        } else {
            this.jobs[i] = event;
        }
    }


    filterJobs() {

        const queryParams = {};
        const payload = {
            categories: [],
            location: this.selectedlocation,
            gender: this.selectedGender
        }
        this.selectedCategories.forEach(element => {
            if (element.type === 'recentJobPostedOptions') {
                queryParams['duration'] = element.id;
            } else {
                payload.categories.push(element.displayName);
            }
        });
        console.log(payload);
        const loader = this.dialog.createLoader();
        this.apiService.getAllJobs(queryParams, payload).then((data: any) => {
            loader.close();
            this.jobs = data;
            this.loader = false;
        }, () => {
            loader.close();
            this.loader = false;
        });
    }

}
