import { Component, OnInit } from '@angular/core';
import { JobsService } from '../jobs.service';
import { ActivatedRoute, Router } from '@angular/router';
import { AppliedJobsComponent } from '../applied-jobs/applied-jobs.component';
import { AuthService } from '../../core/services/auth.service';
import { DialogService } from 'src/app/core/services/dialog.service';
import { CustomFormComponent } from '../../core/components/custom-form/custom-form.component';
import { roleForm } from '../../dashboard/admin-dashboard/create-job/create-role-form';
import { ToastrService } from 'ngx-toastr';
import { ApiService } from '../../core/services/api.service';

@Component({
    selector: 'app-job-details',
    templateUrl: './job-details.component.html',
    styleUrls: ['./job-details.component.scss']
})
export class JobDetailsComponent implements OnInit {
    jobDetails: any = {};
    jobDetailsMin = 0;
    jobDetailsMax = 0;

    otherJobs: any = {};

    actionsFlag = false;
    viewApplied = false;
    locationHref = '';
    constructor(
        public apiService: JobsService,
        private route: ActivatedRoute,
        private dialog: DialogService,
        private router: Router,
        public auth: AuthService,
        public toaster: ToastrService,
        public commonApi: ApiService,
    ) {


    }

    ngOnInit() {
        this.locationHref = location.href

        this.auth.userUpdated.subscribe(() => {
            this.actionsFlag = this.auth._user && (['superAdmin'].includes(this.auth._user.role));
            this.viewApplied = this.auth._user && (['superAdmin', 'client'].includes(this.auth._user.role));
        });

        this.actionsFlag = this.auth._user && (['superAdmin'].includes(this.auth._user.role));
        this.viewApplied = this.auth._user && (['superAdmin', 'client'].includes(this.auth._user.role));

        this.route.paramMap.subscribe(params => {
            const id = params.get('id');

            this.apiService.getJobDetails(id).then((data) => {
                this.jobDetails = data;

                let age = JSON.parse(this.jobDetails.roles[0].age);

                this.jobDetailsMin = age.min
                this.jobDetailsMax = age.max
            });

            this.apiService.getAllJobs({ sortBy: 'createdDate' }).then((data: any) => {

                this.otherJobs = data;
            });
        });


        const loader = this.dialog.createLoader();
        Promise.all([this.commonApi.getCountries(), this.commonApi.getEthnicity()]).then((res) => {
            loader.close();
            roleForm.location['dropdownOptions'] = res[0].data.map((c) => ({ id: c.name, value: c.name }));
            roleForm.ethnicity['dropdownOptions'] = res[1].data.appearance.ethnicLook.map((e) => ({ id: e, value: e }));
        }, () => {
            loader.close();
        });

    }

    showAppliedCandidates(jobId, roleId) {
        const dialogForm = this.dialog.create({
            width: '60%',
            maxWidth: '60%',
            height: '75vh',
            panelClass: 'custom',
            disableClose: true,
            data: {
                title: 'Applied / Selected',
                type: 'custom',
                component: AppliedJobsComponent,
                data: { jobId: jobId, roleId: roleId }
            }
        });
    }

    clearCandidateSelection(role, candidate) {
        role.selectedCandidates = role.selectedCandidates.splice(role.selectedCandidates.findIndex(item => item.id === candidate.id), 1);
        role.selectedCandidates.find((item) => {
            if (item.id === candidate.id) {
                item.checked = false;
            }
        });
    }

    appliedCandidatesSelectionChange(role, candidate) {

    }

    navigateTo() {
        if (this.auth._user && (['superAdmin', 'client'].includes(this.auth._user.role))) {
            this.router.navigate(['dashboard'], { queryParams: { tab: 1 } })
        } else {
            this.router.navigate(['jobs']);
        }
    }

    applyJob(jobId, roleId, role?: any) {
        if (this.auth.isAuthenticated()) {
            const loader = this.dialog.createLoader();
            this.apiService.applyJob(jobId, roleId).then(() => {
                this.toaster.success('Job applied successfully')
                if (role) {
                    role.applied = true;
                }
                loader.close();
            }, (error) => {
                loader.close();
                if (error.error && error.error.message) {
                    this.dialog.createAlert(error.error.message);
                } else {
                    this.dialog.createAlert('Something went wrong. Please try later');
                }
            })
        } else {
            this.auth.redirectUrl = this.router.url;

            // Mark the redirect URL
            sessionStorage.setItem('redirectUrl', this.router.url);

            this.router.navigate(['/login']);
        }
    }

    editRole(role?: any, i?: any) {
        let form = { ...roleForm };

        if (!['Promoters', 'Models', 'Actors', 'Entertainers & Performers'].includes(this.jobDetails.category)) {
            delete form.ethnicity,
                delete form.age
        }
        const dialogForm = this.dialog.createForm({
            type: 'custom',
            title: 'Add Role',
            component: CustomFormComponent,
            panelClass: 'custom',
            dataProperties: {
                inputs: {
                    lookup: form,
                    model: role || {}
                },
                outputs: {
                    submit: (event) => {
                        const loader = this.dialog.createLoader();
                        const payload = { ...this.jobDetails['roles'][i], ...event };
                        this.apiService.updateRole(payload).then((res) => {
                            this.toaster.success('Role updated successfully');
                            loader.close();
                            this.jobDetails['roles'][i] = payload;
                            dialogForm.close();
                        }, () => {
                            loader.close();
                            this.dialog.createAlert('Something went wrong. Please try later!');
                        });
                    }
                }
            }
        });
    }

    createRole(role?: any, i?: any) {
        const dialogForm = this.dialog.createForm({
            type: 'custom',
            title: 'Add Role',
            component: CustomFormComponent,
            panelClass: 'custom',
            dataProperties: {
                inputs: {
                    lookup: roleForm,
                    model: {}
                },
                outputs: {
                    submit: (event) => {
                        const loader = this.dialog.createLoader();
                        const payload = { jobId: this.jobDetails.id, ...event };
                        this.apiService.createRole(payload).then((res) => {
                            this.apiService.getJobDetails(this.jobDetails.id).then((data) => {
                                this.toaster.success('Job updated successfully');
                                this.jobDetails = data;
                                loader.close();
                                dialogForm.close();
                            });
                        }, () => {
                            loader.close();
                            this.dialog.createAlert('Something went wrong. Please try later!');
                        });
                    }
                }
            }
        });
    }

    deleteRole(role, i) {
        if (this.jobDetails.roles.length === 1) {
            this.dialog.createAlert('Atleast one role should be there');
        } else {
            this.dialog.createConfirm(`Are you sure? You want to delete "${role.title}" Role.`, () => {
                const loader = this.dialog.createLoader();
                this.apiService.deleteRole(role.id).then((res) => {
                    this.toaster.success('Role deleted successfully');
                    loader.close();
                    this.jobDetails['roles'].splice(i, 1);
                }, (err) => {
                    loader.close();
                    this.dialog.createAlert('Something went wrong. Please try later');
                })
            });
        }
    }

}
