import { Injectable, createPlatform } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { DialogComponent } from '../components/dialog/dialog.component';
// import { DialogComponent } from '../components/dialog/diaimport { config } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class DialogService {
    constructor(public dialog: MatDialog) { }

    createLoader() {
        return this.dialog.open(DialogComponent, {
            disableClose: true,
            panelClass: ['dialog-transparent'],
            data: {
                type: 'loader'
            }
        });
    }

    createAlert(message) {
        return this.dialog.open(DialogComponent, {
            disableClose: true,
            data: {
                type: 'alert',
                message: message
            }
        });
    }

    createForm(config: any) {
        return this.dialog.open(DialogComponent, {
            position: { right: '0'},
            panelClass: ['h-100'],
            disableClose: true,
            data: config
        });
    }

    createConfirm(message, confirmFn) {
        return this.dialog.open(DialogComponent, {
            disableClose: true, 
            data: {
                type: 'confirm',
                message: message,
                confirm: confirmFn
            }
        })
    }

    create(config: any) {
        return this.dialog.open(DialogComponent, config);
    }

}
