import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpService } from '../../services/http.service';
import { AuthService } from '../../services/auth.service';
import { DialogService } from '../../services/dialog.service';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

    login: FormGroup;

    constructor(
        public router: Router,
        public http: HttpService,
        public auth: AuthService,
        public dialog: DialogService
    ) { }

    ngOnInit() {
        this.login = new FormGroup({
            email: new FormControl('', [Validators.required]),
            password: new FormControl('', [Validators.required ]),
            rememberMe: new FormControl()
        });
    }

    hasError(controlName: string, errorName: string) {
        return this.login.controls[controlName] ? this.login.controls[controlName].hasError(errorName) : false;
    }

    loginUser(user) {
        if (this.login.valid) {
            const loader = this.dialog.createLoader();
            this.http.post('api/v1/user/login', user).then((response) => {
                this.auth.setAuth(response.data, user.rememberMe);
                this.auth.setUser(response.data);
                loader.close();
                const url = this.auth.redirectUrl || sessionStorage.getItem('redirectUrl') || '/dashboard'
                this.router.navigate([url]);
            }, (error) => {
                loader.close();
                error = error.error;
                if (typeof error.message !== 'string') {
                    error.message = JSON.stringify(error.message, null, 4);
                }
                this.dialog.createAlert(error.message);
            });
        }
    }

    goToSignUp() {
        this.router.navigate(['/signup']);
    }
}
