import {
  Directive, ElementRef, Input, Renderer2, AfterViewInit, HostListener, OnChanges
} from '@angular/core';

@Directive({
  selector: '[appCharCount]'
})

export class CharCountDirective implements AfterViewInit, OnChanges {
  @Input() appCharCount: number;
  @Input() ngModel: any;

  counter: any;
  count: number;

  constructor(private element: ElementRef, private renderer: Renderer2) {
  }

  @HostListener('keyup', ['$event'])
  inputKeyup($event) {
      if ($event && $event.currentTarget) {
          this.count = this.appCharCount - $event.currentTarget.value.length;
          this.updateCount(this.count);
      }
  }

  ngOnChanges($event) {
      if ($event && $event.ngModel && $event.ngModel.currentValue) {
          this.count = this.appCharCount - $event.ngModel.currentValue.length;
          this.updateCount(this.count);
      }
  }

  ngAfterViewInit() {
      // TODO There is probably a better, event-based hook to get the initial ngModel value... this works for now.
      setTimeout(() => {
          const currentValue = this.element.nativeElement.value;
          this.count = typeof currentValue !== 'undefined' ? this.appCharCount - currentValue.length
              : this.appCharCount;
          this.updateCount(this.count);
      });

      this.counter = this.renderer.createElement('span');
      this.renderer.addClass(this.counter, 'character-count');
      this.renderer.setStyle(this.counter, 'position', 'absolute');
      this.renderer.setStyle(this.counter, 'top', '-5px');
      this.renderer.setStyle(this.counter, 'right', '2px');
      this.renderer.setStyle(this.counter, 'font-size', '11px');
      this.renderer.setStyle(this.counter, 'color', '#68152A');
      this.renderer.insertBefore(this.element.nativeElement.parentNode, this.counter, this.element.nativeElement);
  }

  updateCount(count) {
      this.renderer.setProperty(this.counter, 'innerHTML', `${count} Characters`);
  }
}
