export class ProfileDetails {
  personalDetails = {
    name: '',
    image: '',
    aboutMe: '',
    professionalDetails: '',
    ethnicity: '',
    categories: [],
    dob: '',
    nationality: '',
    gender: '',
    location: '',
    phoneNumber: '',
    email: ''
  };

  appearance = {
    ethnicLook: '',
    hairType: '',
    hairColor: '',
    eyeColor: '',
    height: '',
    shoeSize: '',
    tshirtSize: '',
    pantSize: '',
    jacketSize: '',
    dressSize: '',
    chest: '',
    waist: '',
    hips: '',
    // performance: '',
    tattoos: '',
    piercings: '',
    facialHair: '',
  };

  arts = {
    languages: [],
    singing: [],
    instruments: [],
    dance: [],
    vocalRange: []
  };

  sports = {
    waterSports: [],
    winterSports: [],
    gymnastics: [],
    teamSports: [],
    miscSports: [],
    swimming: [],
    trackAndField: [],
    cycling: [],
    stageCombatTraining: [],
    martialArts: [],
    martialArtsWeaponsTraining: [],
    generalWeaponsTraining: []
  };

  // features = {
  //     willingToTravel : '',
  //     wardrobeItems: []
  // };

  // combatTraining = {
  //     stageCombatTraining: [],
  //     martialArts: [],
  //     martialArtsWeaponsTraining: [],
  //     generalWeaponsTraining: []
  // };

  // otherSkills = {
  //     specialFeatures: [],
  //     drivingSkills: [],
  //     improvisation: [],
  //     circusSkills: [],
  //     horseRidingSkills: [],
  //     smoking: [],
  //     miscellaneousSkills: []
  // };

  experience = [];

  constructor(partialDetails: any, removeNull = true) {
    const personalDetails = ['name', 'image', 'aboutMe', 'professionalDetails', 'ethnicity', 'categories', 'dob', 'nationality', 'gender', 'location', 'phoneNumber', 'email'],
      appearance = ['ethnicLook', 'hairType', 'hairColor', 'eyeColor', 'height', 'shoeSize', 'tshirtSize', 'pantSize', 'jacketSize', 'dressSize', 'chest', 'waist', 'hips', 'tattoos', 'piercings', 'facialHair'],
      arts = ['languages', 'singing', 'instruments', 'dance', 'vocalRange'],
      sports = ['waterSports', 'winterSports', 'gymnastics', 'teamSports', 'miscSports', 'swimming', 'trackAndField', 'cycling', 'stageCombatTraining', 'martialArts', 'martialArtsWeaponsTraining', 'generalWeaponsTraining'];
    // features = ['willingToTravel','wardrobeItems'],
    // combatTraining = ['stageCombatTraining','martialArts','martialArtsWeaponsTraining','generalWeaponsTraining'],
    // otherSkills = ['specialFeatures','drivingSkills','improvisation','circusSkills','horseRidingSkills','smoking','miscellaneousSkills'];

    if (partialDetails) {
      for (const key in partialDetails) {
        if (personalDetails.includes(key)) {
          this.personalDetails[key] = partialDetails[key];
        } else if (appearance.includes(key)) {
          this.appearance[key] = partialDetails[key];
        } else if (arts.includes(key)) {
          this.arts[key] = partialDetails[key];
        } else if (sports.includes(key)) {
          this.sports[key] = partialDetails[key];
          // } else if (features.includes(key)) {
          //     this.features[key] = partialDetails[key];
          // } else if (combatTraining.includes(key)) {
          //     this.combatTraining[key] = partialDetails[key];
          // } else if (otherSkills.includes(key)) {
          //     this.otherSkills[key] = partialDetails[key];
        } else {
          this[key] = partialDetails[key];
        }
      }
    }
    //, 'features', 'combatTraining', 'otherSkills'
    ['appearance', 'arts', 'sports'].forEach((e) => {
      let flag = true;

      for (let k in this[e]) {
        if ((Array.isArray(this[e][k]) && this[e][k].length) || (!Array.isArray(this[e][k]) && this[e][k])) {
          flag = false;
        }
      }

      if (flag) {
        delete this[e];
      }
    });
  }
}
