import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class AuthService {

    redirectUrl: string;
    queryParams: any;
    _user: any;
    _user$ = new Subject();

    get userUpdated() {
        return this._user$.asObservable();
    }
    constructor() { }

    setAuth(user, remember) {
        if (remember) {
            localStorage.setItem('id', user.userId);
            localStorage.setItem('token', user.token);
            localStorage.setItem('role', user.role);
        } else {
            sessionStorage.setItem('id', user.userId);
            sessionStorage.setItem('token', user.token);
            sessionStorage.setItem('role', user.role);
        }
    }

    isAuthenticated() {
        const id = sessionStorage.getItem('id') || localStorage.getItem('id');
        const token = sessionStorage.getItem('token') || localStorage.getItem('token');
        const role = sessionStorage.getItem('role') || localStorage.getItem('role');
        return !!id && !!token && !!role;
    }

    getToken() {
        return sessionStorage.getItem('token') || localStorage.getItem('token');
    }

    getUserId() {
        return sessionStorage.getItem('id') || localStorage.getItem('id');
    }

    getRole() {
        return sessionStorage.getItem('role') || localStorage.getItem('role');
    }

    signout() {
        localStorage.removeItem('id');
        localStorage.removeItem('token');
        sessionStorage.removeItem('id');
        sessionStorage.removeItem('token');
        this._user = {};
    }

    setUser(user) {
        this._user = {email: user.email, firstName: user.firstName, lastName: user.lastName, role: user.role, image: user.image };
        this._user$.next();
    }
}
