import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { HttpService } from '../../services/http.service';
import { DialogService } from '../../services/dialog.service';
import { Router } from '@angular/router';

@Component({
    selector: 'app-signup',
    templateUrl: './signup.component.html',
    styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {

    signup: FormGroup;
    showAgentDetails: boolean = false;

    constructor(
        public http: HttpService,
        public dialog: DialogService,
        public router: Router
    ) { }

    ngOnInit() {
      console.log('hello i am singup')
        this.signup = new FormGroup({
            firstName: new FormControl('', [Validators.required, Validators.maxLength(60)]),
            lastName: new FormControl('', [Validators.required, Validators.maxLength(60)]),
            email: new FormControl('', [Validators.required, Validators.email]),
            password: new FormControl('', [Validators.required]),
            confirmPassword: new FormControl('', [Validators.required, this.checkPasswordMatch.bind(this)]),
            hearAboutUs: new FormControl('', [Validators.required])
        });
    }

    ngAfterViewInit() {
        this.signup.valueChanges
            .subscribe(values => {
                if (values['hearAboutUs'] === 'agent') {
                    if (!this.signup.get('agentName')) {
                        this.signup.addControl('agentName', new FormControl('', [Validators.required]));
                    }
                    if (!this.signup.get('agentContact')) {
                        this.signup.addControl('agentContact', new FormControl('', [Validators.required]));

                    }
                    this.showAgentDetails = true;
                } else {
                    this.showAgentDetails = false;
                    if (this.signup.get('agentName')) {
                        this.signup.removeControl('agentName');
                    }
                    if (this.signup.get('agentContact')) {
                        this.signup.removeControl('agentContact');
                    }
                }
            });
    }

    checkPasswordMatch(control: AbstractControl) {
        if (this.signup) {
            const password = this.signup.get('password').value;
            return password !== control.value ? { notMatched: { value: control.value } } : null;
        }
        return null;
    }

    hasError(controlName: string, errorName: string) {
        return this.signup.controls[controlName].hasError(errorName);
    }

    registerUser(user) {
      console.log(user);
        const loader = this.dialog.createLoader();

        if (this.signup.valid) {
            this.http.post('api/v1/user/register', user)
                .then((response) => {
                    loader.close();
                    this.router.navigate(['confirm-register/success']);
                }, (error) => {
                    loader.close();
                    this.dialog.createAlert('Unable to register! Please try after sometime!');
                });
        }
    }
}
