import { Component, OnInit, Input } from '@angular/core';
import { NgxFileDropEntry, FileSystemFileEntry, FileSystemDirectoryEntry } from 'ngx-file-drop';
import { DialogService } from 'src/app/core/services/dialog.service';
import { CarouselComponent } from 'src/app/core/components/carousel/carousel.component';
import { UserDashboardService } from '../user-dashboard.service';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-portfolio',
    templateUrl: './portfolio.component.html',
    styleUrls: ['./portfolio.component.scss']
})
export class PortfolioComponent implements OnInit {
    mediaType = 'image';
    images = [];
    videos = [];
    audios: any[];
    files: any;
    showMoreDetails = false;
    limits = {};
    description ='';

    @Input() readonlyPortfolio = false;
    @Input() id: any;

    get getMediaDetails() {
        if (this.mediaType === 'image') {
            return this.images;
        }
        if (this.mediaType === 'video') {
            return this.videos;
        }
        if (this.mediaType === 'audio') {
            return this.audios;
        }
        return [];
    }

    get acceptType() {
        if (this.mediaType === 'image') {
            return ['png', 'jpeg', 'jpg', 'PNG', 'JPEG', 'JPG'];
        }
        if (this.mediaType === 'video') {
            return ['mp4', 'avi', 'mov', 'flv', 'wmv'];
        }
        if (this.mediaType === 'audio') {
            return ['m4a', 'mp3', 'wav'];
        }
        return [];
    }

    get uploadableCount() {
        return this.limits['max_'+this.mediaType+'s'] - this.limits[this.mediaType+'s'];
    }

    constructor(public dialog: DialogService, public apiService: UserDashboardService, public toaster: ToastrService, private router: ActivatedRoute) { }

    ngOnInit() {
        const user = this.router.snapshot.paramMap.get('user') || this.id;
        const loader = this.dialog.createLoader();
        Promise.all([this.apiService.getPortfolio(user), this.apiService.getPortfolioCounts(this.router.snapshot.paramMap.get('user'))]).then((res) => {
            loader.close();
            this.setPortfolio(res[0].data || {});
            this.limits = res[1].user || {};
        }, (error) => {
            loader.close();
            this.dialog.createAlert('Something went wrong');
        });

    }

    setPortfolio(data) {
        this.images = data.images ? (JSON.parse(data.images) || []).map(e => ({url : e})) : [];
        this.videos = data.videos ? (JSON.parse(data.videos) || []).map(e => ({url : e.files, descriptions : e.description})) : [];
        this.audios = data.audios ?  (JSON.parse(data.audios) || []).map(e => ({url : e.files, descriptions : e.description})) : [];
    }
    onDataChange($event : any){
        this.description = $event;
    }

   async dropped(files: NgxFileDropEntry[]) {
        let nonAcceptFiles = [];

        this.files = files;
        console.log(files);

        if (this.limits[this.mediaType+'s']+files.length > this.limits['max_'+this.mediaType+'s']) {
            this.dialog.createAlert('Please select within given upload limit.');
            return;
        }
        let apiService = this.apiService;
        let formData = new FormData();
        formData.append('description',this.description);
        for (const droppedFile of files) {
            // Is it a file?
            if (droppedFile.fileEntry.isFile) {
                let fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
                await fileEntry.file((file: File) => {
                   let ext = (droppedFile.relativePath.split('.')).pop();
                    if (this.acceptType.includes(ext)) {
                        formData.append(this.mediaType, file, droppedFile.relativePath);
                    } else {
                        nonAcceptFiles.push(droppedFile.relativePath);
                    }
                });
            } else {
                // It was a directory (empty directories are added, otherwise only files)
                let fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
                console.log(droppedFile.relativePath, fileEntry);
            }
        }
        let loader = this.dialog.createLoader();
        let acceptype = this.acceptType.join();
        let dialog = this.dialog;
        let toaster = this.toaster;
        let ngOnInit = () => {this.ngOnInit()};
    setTimeout(async function(){

      const userId = this.router.snapshot.paramMap.get('user') || null;
        apiService.portfolio(formData, userId).then((res) => {
            if (nonAcceptFiles.length) {
                dialog.createAlert('Unable to upload '+nonAcceptFiles.join()+' files. Please upload '+acceptype+' type files only.');
            }
            ngOnInit();
            toaster.success(`Your files uploaded successfully`);
            loader.close();
        }, () => {
            loader.close();
            dialog.createAlert('invalid file upload!');
        });
    }, 1000);
}

    public saveFiles(formData, nonAcceptFiles){
      const userId = this.router.snapshot.paramMap.get('user') || null;
        let loader = this.dialog.createLoader();
        this.apiService.portfolio(formData, userId).then((res) => {
            if (nonAcceptFiles.length) {
                this.dialog.createAlert('Unable to upload '+nonAcceptFiles.join()+' files. Please upload '+this.acceptType.join()+' type files only.');
            }
            this.ngOnInit();
            this.toaster.success(`Your files uploaded successfully`);
            loader.close();
        }, () => {
            loader.close();
            this.dialog.createAlert('something went wrong');
        });
    }

    public fileOver(event) {
        console.log(event);
    }

    public fileLeave(event) {
        console.log(event);
    }

    showDetails(data: any[], item: any) {
        const copy = [...data];
        const index = copy.findIndex(e => e.url === item.url);
        const half = copy.splice(index);

        this.showMoreDetails = true;
        this.dialog.create({
            width: '95%',
            maxWidth: '95%',
            height: '95vh',
            panelClass: 'custom',
            data: {
                component: CarouselComponent,
                type: 'custom',
                dialogClose: true,
                dataProperties: {
                    inputs : {
                        data: half.concat(copy),
                        type: this.mediaType,
                        loop: true
                    }
                }
            }
        });
    }

    deleteMedia(url, media) {

        const loader = this.dialog.createLoader();
        if(media == "videos"){
            url = url.toString();
        }

        this.apiService.deleteMedia(this.router.snapshot.paramMap.get('user'),{type: media, filePath: url}).then((res) => {
            this.ngOnInit();
            this.toaster.success(`Successfully deleted`);
            loader.close();
        }, () => {
            loader.close();
            this.dialog.createAlert('something went wrong');
        });
    }

    playVideo(video) {

    }

    playAudio(event, audio) {
        const audios: any = document.querySelectorAll('audio');
        audios.forEach(x => {
            x.pause();
        });
        this.audios.forEach(x => {
            x['show'] = false;
        })
        audio.show = true;
    }

}
