import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../core/services/auth.service';
import { ApiService } from '../../core/services/api.service';
import { jobForm } from './edit-job-form';
import { DialogService } from '../../core/services/dialog.service';
import { CustomFormComponent } from '../../core/components/custom-form/custom-form.component';
import { JobsService } from '../jobs.service';
import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'app-job-entity',
    templateUrl: './job-entity.component.html',
    styleUrls: ['./job-entity.component.scss']
})
export class JobEntityComponent implements OnInit {
    @Input() data: any;
    @Input() index: any;
    @Output() afterUpdate = new EventEmitter();
    actionsFlag = false;
    subCategories = {};
    description = '';

    get isAdmin() {
        return this.auth._user && this.auth._user.role && (['superAdmin'].includes(this.auth._user.role));
    }
    constructor(
        private router: Router,
        public auth: AuthService,
        public commonApi: ApiService,
        public dialog: DialogService,
        public apiService: JobsService,
        public toaster: ToastrService
    ) { }

    ngOnInit() {
        this.actionsFlag = this.auth.isAuthenticated() && this.auth._user && (['superAdmin'].includes(this.auth._user.role));

        this.description = this.extractContent(this.data.description);

        Promise.all([this.commonApi.getCountries(), this.commonApi.getCategories()]).then((res) => {
            jobForm.location['dropdownOptions'] = res[0].data.map((e) => ({ id: e.name, value: e.name }));
            jobForm.location['change'] = ((event) => {
                const loader = this.dialog.createLoader();
                this.commonApi.getCities(event.value).then((res) => {
                    jobForm.city['dropdownOptions'] = res.data.map((e) => ({ id: e.name, value: e.name }));
                    loader.close();
                }, (error) => {
                    this.dialog.createAlert('Something went wrong!');
                    loader.close();
                })
            }).bind(this);
            res[1].data.forEach((c) => {
                this.subCategories[c.category] = c.subCategories;
            })


            jobForm.category['dropdownOptions'] = Object.keys(this.subCategories).map(e => ({ id: e, value: e }));
            // jobForm.category['change'] = ((event) => {
            //     jobForm.subCategory['dropdownOptions'] = this.subCategories[event.value].map(e => ({ id: e, value: e }));
            // }).bind(this);
        });
    }

    extractContent(s) {
        const span = document.createElement('span');
        span.innerHTML = s;
        return span.textContent || span.innerText;
    };

    showJobDetails(data) {
        this.router.navigate([`job/${data.id}`]);
    }

    editJob(data) {
        const loader = this.dialog.createLoader();
        Promise.all([this.commonApi.getCities(data.location), this.commonApi.getEthnicity()]).then((res) => {
            jobForm.city['dropdownOptions'] = res[0].data.map((e) => ({ id: e.name, value: e.name }));
            // jobForm.subCategory['dropdownOptions'] = this.subCategories[data.category].map(e => ({ id: e, value: e }));
            loader.close();

            jobForm.ethnicity['dropdownOptions'] = JSON.parse(res[1].data.appearance.ethnicLook).map((e) => ({ id: e, value: e }));
            console.log(data);
            data = { ...data, ...{ gender: data.roles[0].gender, age: data.roles[0].age, ethnicity: data.roles[0].ethnicity, salaryRange: data.roles[0].salaryRange } }

            const dialogForm = this.dialog.createForm({
                type: 'custom',
                title: 'Edit Job',
                component: CustomFormComponent,
                panelClass: 'custom',
                dataProperties: {
                    inputs: {
                        lookup: jobForm,
                        model: data
                    },
                    outputs: {
                        submit: (event) => {
                            console.log(event);
                            const loader = this.dialog.createLoader();
                            const payload = { ...data, ...event };
                            this.apiService.updateJob(payload).then(() => {
                                const role = { ...data.roles[0], ...{ gender: event.gender, age: event.age, ethnicity: event.ethnicity, salaryRange: event.salaryRange } };
                                payload.roles[0] = { ...role };
                                this.apiService.updateRole(role).then(() => {
                                    this.afterUpdate.emit(payload);
                                    this.toaster.success('Job updated successfully');
                                    loader.close();
                                }, () => {
                                    this.dialog.createAlert('Something went wrong');
                                    loader.close();
                                });
                            }, () => {
                                this.dialog.createAlert('Something went wrong');
                                loader.close();
                            });
                            dialogForm.close();
                        }
                    }
                }
            });
        }, () => {
            this.dialog.createAlert('Something went wrong!');
            loader.close();
        });

    }

    deleteJob(data) {
        this.dialog.createConfirm(`Are you sure? You want to delete "${data.title}" Job.`, () => {
            const loader = this.dialog.createLoader();
            this.apiService.deleteJob(data.id).then((res) => {
                this.toaster.success('Job deleted successfully');
                loader.close();
                this.afterUpdate.emit('deleted');
            }, (err) => {
                loader.close();
                this.dialog.createAlert('Something went wrong. Please try later');
            })
        })
    }
}
