import { Injectable } from '@angular/core';
import { HttpService } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(
    public http: HttpService
  ) { }

  getTalents(payload, isAdmin = false) {
    if (isAdmin) {
      return this.http.get('api/v1/all-talents', payload, {auth: true});
    }
    return this.http.get('api/v1/talents', payload);
  }

  getCountries() {
    return this.http.get('api/v1/countries', {});
  }

  getCities(event) {
      return this.http.get(`api/v1/cities/${event}`, {});
  }

  getEthnicity() {
    return this.http.get('api/v1/user/dropdown', {});
  }

  getCategories() {
    return this.http.get('api/v1/job/categories', {});
  }

  getParentCategories() {
    return this.http.get('api/v1/categories', {});
  }

  getProfilePerCategories(payload = {}) {
    const url = 'api/v1/categories/profile';
    return new Promise((resolve, reject) => {
        this.http.get(url, payload).then((response) => {
            resolve(response.data);
        }, reject);
    });
  }

  forgotPassword(payload) {
    return this.http.put('api/v1/users/forgotpassword', payload);
  }
}
