import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { DialogService } from '../../../core/services/dialog.service';
import { CustomFormComponent } from '../../../core/components/custom-form/custom-form.component';
import { roleForm } from './create-role-form';
import { AdminDashboardService } from '../admin-dashboard.service';
import { ApiService } from '../../../core/services/api.service';
import { ToastrService } from 'ngx-toastr';
import { AddJobImageComponent } from './add-job-image/add-job-image.component';

@Component({
  selector: 'app-create-job',
  templateUrl: './create-job.component.html',
  styleUrls: ['./create-job.component.scss']
})
export class CreateJobComponent implements OnInit {

  currentTabIndex: number = 0;
  selectedTabIndex: number = 0;
  categories: any;
  subCategories = {};
  countries: any;
  castingCall: FormGroup;
  additionalInfoForm: FormGroup;
  roleDetails: FormGroup;
  roles = [];
  cities = [];
  jobImage: any;
  previewImage: any = '/assets/img/job_posting/defaultJob.jpg';
  clientList: any = [];
  ethnicities: any = [];
  isAgeInvalid: boolean;
  
  constructor(public dialog: DialogService, public apiService: AdminDashboardService, public commonApi: ApiService, public toaster: ToastrService) { }

  ngOnInit() {
    
    this.setFormGroups();
    const loader = this.dialog.createLoader();
    Promise.all([this.commonApi.getCategories(), this.commonApi.getCountries(), this.commonApi.getEthnicity(), this.apiService.getClientsList(),this.commonApi.getParentCategories()]).then((res: any) => {
      // res[4].data.forEach((c) => {
      //   this.subCategories[c.category] = ;
      // })
      // this.categories = Object.keys(this.subCategories);
      this.categories = res[0].data.map(e => ({ type:"category", id: e.category, displayName: e.category }));
      console.log(this.categories);
      this.countries = res[1].data;
      loader.close();

      roleForm.location['dropdownOptions'] = this.countries.map((c) => ({id: c.name, value: c.name}));
      this.ethnicities = JSON.parse(res[2].data.appearance.ethnicLook).map((e) => ({id: e, value: e})); 

      this.clientList = res[3].map(e => e.email);
      if (!this.clientList) {
        this.dialog.createAlert('No Clients were created. Please create a client first.');
      }
    });
  }

  setFormGroups() {
    
    this.castingCall = new FormGroup({
      title: new FormControl('', [Validators.required]),
      location: new FormControl('United Arab Emirates', [Validators.required]),
      city: new FormControl('', [Validators.required]),
      startDate: new FormControl('', [Validators.required]),
      endDate: new FormControl('', [Validators.required]),
      description: new FormControl('', []),
      type: new FormControl('Full Time', [Validators.required]),
      category: new FormControl('Actors', [Validators.required]),
      subCategory: new FormControl('Feature Films', [this.isSubCategoryRequired.bind(this)]),
      clientEmail: new FormControl('', [Validators.required, Validators.email]),
      expiryDate: new FormControl('', [Validators.required]),
      applyFrom: new FormControl('anyOne', [Validators.required]),
      applyFromLocation: new FormControl('', [this.specificPeopleValidation.bind(this)]),
      
      // moreDetails: new FormControl('', [])
    });
    this.additionalInfoForm = new FormGroup({
      // expiryDate: new FormControl('', [Validators.required]),
      // applyFrom: new FormControl('anyOne', [Validators.required]),
      // applyFromLocation: new FormControl('', [this.specificPeopleValidation.bind(this)]),
      // moreDetails: new FormControl('', [])
    });
    this.roleDetails = new FormGroup({
      gender: new FormControl('both', [Validators.required]),
      age: new FormGroup({
        min: new FormControl(0, []),
        max: new FormControl(50, [])
      }),
      ethnicity: new FormControl('', []),
      salaryRange: new FormControl('To be decided', [Validators.required])
    });
  }

  validateAge() {
    this.isAgeInvalid = this.roleDetails.value.age.min > this.roleDetails.value.age.max;
  };

  isSubCategoryRequired(control: AbstractControl) {
      if (this.castingCall) {
          const category = this.castingCall.get('category').value;
          return this.subCategories[category] && !this.subCategories[category].length ? { required: {value: control.value}} : null;
      }
      return null;
  }

  specificPeopleValidation(control: AbstractControl) {
    if (this.castingCall) {
      return this.castingCall.get('applyFrom').value === 'anyOne' || this.castingCall.get('applyFrom').value ? null : { required: {value: control.value}};
    }
    return null;
  }

  onSelectedIndexChange(index) {
    this.currentTabIndex = index;
    this.selectedTabIndex = index;
  }

  hasError(form: any, controlName: string, errorName: string) {
    return form.controls[controlName].hasError(errorName);
  }

  changeStep(tabChange = 1, form?:any) {
    if (tabChange) {
      this.selectedTabIndex = this.currentTabIndex + tabChange;
    } else {
      // if (!this.roles.length) {
      //   this.dialog.createAlert('There should be atleast one role');
      //   return;
      // }
      
      const loader = this.dialog.createLoader();
      this.apiService.createJob({...this.castingCall.value, ...this.additionalInfoForm.value, ...{roles: [this.roleDetails.value]}}).then((res) => {
        this.apiService.uploadJobImage(this.jobImage, res.data.id).then(() => {
          loader.close();
          this.onSelectedIndexChange(0);
          this.toaster.success(`"${this.castingCall.value.title}" Job created successfully`);
          if (form) {
            form.casting.resetForm();
          }
          this.setFormGroups();
          this.ethnicities = [];
          this.roles = [];
          this.jobImage = null;
          this.previewImage = null;
        }, () => {
          loader.close();
          this.onSelectedIndexChange(0);
          this.toaster.success(`"${this.castingCall.value.title}" Job created successfully`);
          if (form) {
            form.casting.resetForm();
          }
          this.setFormGroups();
          this.roles = [];
          this.jobImage = null;
          this.previewImage = null;
          this.dialog.createAlert('Something went wrong! Image not set');
        });
        
      }, () => {
        loader.close();
        this.dialog.createAlert('Something went wrong! Please try later.');
      })
    }
  }

  formSubmit(formValue) {
    // console.log(formValue);
  }

  createRole(role?: any, i?: any) {
    let form = {...roleForm};

    if (!['Promoters', 'Models', 'Actors', 'Entertainers & Performers'].includes(this.castingCall.get('category').value)) {
      delete form.ethnicity,
      delete form.age
    }

    const dialogForm = this.dialog.createForm({
      type: 'custom',
      title: 'Add Role',
      component: CustomFormComponent,
      panelClass: 'custom',
      dataProperties: {
        inputs : {
          lookup: form,
          model: role || {}
      },
      outputs : {
          submit: (event) => {
              if (role && typeof i === 'number') {
                this.roles[i] = event;
              } else {
                this.roles.push(event);
              }
              dialogForm.close();
          }
      }
      }
    });
  }

  deleteRole(role, i) {
    this.roles.splice(i,1);
  }

  getCities(event) {
    const loader = this.dialog.createLoader();
    this.commonApi.getCities(event.value).then((res) => {
      this.cities=res.data;
      loader.close();
    });
  }

  addImage() {
    const context = this;
    const dialog = this.dialog.create({
        disableClose: true,
        data: {
            type: 'custom',
            title: 'Add Job Image',
            component: AddJobImageComponent,
            dataProperties: {
                outputs: {
                    afterUpload: function (event) {
                      context.jobImage = event;
                      var reader  = new FileReader();

                      reader.addEventListener("load", function () {
                        context.previewImage = reader.result;
                      }, false);

                      if (event) {
                        reader.readAsDataURL(event);
                      }
                      dialog.close();
                    }
                }
            }
        }
    });
  }
}
