import {Component, OnInit, Output, EventEmitter, ViewChild, Input} from '@angular/core';
import {ImageCropperComponent, ImageCroppedEvent} from 'ngx-image-cropper';
import {UserDashboardService} from '../../user-dashboard.service';
import {DialogService} from '../../../../core/services/dialog.service';
import {ToastrService} from 'ngx-toastr';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-edit-profile-pic',
  templateUrl: './edit-profile-pic.component.html',
  styleUrls: ['./edit-profile-pic.component.scss']
})
export class EditProfilePicComponent implements OnInit {

  dismiss: any;
  @Input() disableCancel = false;
  @Output() afterUpload = new EventEmitter();

  constructor(public apiService: UserDashboardService,
              public dialog: DialogService,
              public toaster: ToastrService,
              private router: ActivatedRoute
  ) {
  }

  ngOnInit() {
  }

  imageChangedEvent: any = '';
  croppedImage: any = '';
  showCropper = false;
  disableUpload = true;

  @ViewChild(ImageCropperComponent) imageCropper: ImageCropperComponent;

  fileChangeEvent(event: any): void {
    this.disableUpload = true;
    this.imageChangedEvent = event;
  }

  imageCropped(event: ImageCroppedEvent) {
    console.log(event);
    this.croppedImage = event.file;
  }

  imageLoaded() {
    this.showCropper = true;
    this.disableUpload = false;
    //console.log('Image loaded')
  }

  cropperReady() {
    this.disableUpload = false;
    //console.log('Cropper ready')
  }

  loadImageFailed() {
    this.disableUpload = true;
    //console.log('Load failed');
  }

  rotateLeft() {
    this.imageCropper.rotateLeft();
  }

  rotateRight() {
    this.imageCropper.rotateRight();
  }

  flipHorizontal() {
    this.imageCropper.flipHorizontal();
  }

  flipVertical() {
    this.imageCropper.flipVertical();
  }

  uploadImage() {
    const userId = this.router.snapshot.paramMap.get('user') || null;
    const loader = this.dialog.createLoader();
    this.apiService.uploadUserImage(this.croppedImage, userId).then((res) => {
      this.afterUpload.emit(res.filePath);
      this.toaster.success('Profile Picture updated successfully');
      loader.close();
    }, (err) => {
      this.dialog.createAlert('Something went wrong. Please try after sometime.');
    });
  }
}
