    constructor(
        private cdr: ChangeDetectorRef,
        public router: Router,
        public apiService: JobsService,
        public dialog: DialogService
    ) { }

    ngOnInit() {
        const loader = this.dialog.createLoader();
       
        this.apiService.getAllJobs({ sortBy: 'createdDate' }).then(async (data: any) => {
            loader.close();
            this.getWorks();
            let i = 0;
            this.recentJobsCarouselTileItems = await data.map((e) => {
                const createDate = moment(e.createdDate);
                const expDate = moment(e.expiryDate);
                const toDate = moment(new Date());

                const obj = {
                    title: e.title,
                    added: toDate.diff(createDate, 'days'),
                    expires: expDate.diff(toDate, 'days'),
                    imageUrl: e.image,
                    id: e.id,
                    content: e.description
                };
                i++;
                if (i > 2) {
                    i = 0;
                }
                return obj;
            });
            this.recentJobsCarouselTileItems = this.recentJobsCarouselTileItems.slice(0, 6);
        }, () => {
            loader.close();
        });
    }

    getWorks() {
        this.apiService.getWorks()
            .then((response: any) => {
                this.latestVideos = response
            }, (err) => {
                this.latestVideos = [];
            })
    }

    ngAfterViewInit() {
        this.cdr.detectChanges();
    }

    navigateTo(link) {
        this.router.navigate([`/${link}`]);
    }
