import { Injectable, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { AuthService } from './auth.service';

// const baseUrl = "http://localhost:3000/";
const baseUrl = "http://67.205.178.177/";
@Injectable({
    providedIn: 'root'
})
export class HttpService implements OnInit {


    constructor(protected http: HttpClient,
        protected authService: AuthService,
    ) {
    }

    ngOnInit() { }

    // --- Utility methods ---
    createHeaders(config?: any) {
        const accept = config && config.accept ? config.accept : 'application/json';
        const contentType = config && config.contentType ? config.contentType : 'application/json';
        let headers = new HttpHeaders()
            .set('Accept', accept);
            //.set('Auth-Token', this.authService.getToken());

        // for multipart/form-data type, leave the content-type blank since browser
        // will dynamically calculate the form-data boundary and append 'boundary--xxx' to type
        if (contentType !== 'multipart/form-data') {
            headers = headers.set('Content-Type', contentType);
        }

        if (config && config.auth) {
            headers = headers.set('authorization', this.authService.getToken());
        }

        if (config && config.reportProgress) {
            headers = headers.set('reportProgress', "true");
        }



        return headers;
    }

    createSearchParams(payload?: any) {
        payload = payload || {};

        let params = new HttpParams();
        // avoid browser caching
        params = params.set('t', new Date().getTime().toString());

        for (const i in payload) {
            // If array is { value: ['a', 'b'] }, then we'll want to convert this to ?value=a&value=b
            if (Array.isArray(payload[i])) {
                params = params.append(i, payload[i].join(','));

            } else {
                params = params.append(i, payload[i]);
            }
        }
        return params;
    }

    createOptions(payload?: any, headerOptions?: any, extra?: any) {
        const headers = this.createHeaders(headerOptions);
        const params = this.createSearchParams(payload);

        let options = {
            headers: headers,
            params: params
        };

        if (extra != null) options = { ...options, ...extra };
        return options;
    }

    // --- CRUD methods ---

    __getObservable(url: string, payload?: any) {

        return this.http.get(url, this.createOptions(payload));
    }

    __get(url: string, payload?: any, headerOptions?: any, extra?: any) {
        return this.http.get(url, this.createOptions(payload, headerOptions, extra)).toPromise();
    }

    __patch(url: string, payload?: any) {
        return this.http.patch(url, payload, this.createOptions()).toPromise();
    }

    __post(url: string, payload: any, headerOptions?: any, queryParams?: any, extra?: any): Promise<any> {
        return this.http.post(url, payload, this.createOptions(queryParams, headerOptions, extra)).toPromise();
    }

    __put(url: string, payload: any, headerOptions?: any, contentType?: string) {
        return this.http.put(url, payload, { headers: this.createHeaders({ contentType: contentType, ...headerOptions }) }).toPromise();
    }

    __delete(url: string, payload?: any, headerOptions?: any, extra?: any) {
        return this.http.delete(url, this.createOptions(payload, headerOptions, extra)).toPromise();
    }

    // --- Commonly used methods ---

    /**
     * Handle API errors.
     * @param error
     * @returns {Promise<void>|Promise<T>}
     */
    handleError(error: any): Promise<any> {
        return Promise.reject(error);
    }

    get(url: string, payload?: any, headerOptions?: any): Promise<any> {
        const URL = baseUrl+url;
        return this.__get(URL, payload, headerOptions)
            .catch(this.handleError);
    }

    post(url: string, payload: any, headerOptions?: any): Promise<any> {
        const URL = baseUrl+url;
        return this.__post(URL, payload, headerOptions)
            .catch(this.handleError);
    }

    put(url: string, payload?: any, headerOptions?: any, contentType?: any): Promise<any> {
        const URL = baseUrl+url;
        return this.__put(URL, payload, headerOptions, contentType)
            .catch(this.handleError);
    }

    delete(url: string, payload?: any, headerOptions?: any) {
        const URL = baseUrl+url;
        return this.__delete(URL, payload, headerOptions)
            .catch(this.handleError);
    }

    // __get images blobs and return when all have completed
    getImages(url, images) {
        const URL = baseUrl+url;
        const imgHeaders = new HttpHeaders({
            'Accept': 'image/png',
            'Auth-Token': this.authService.getToken()
        });

        const params = new HttpParams();
        params.set('responseType', 'blob');

        const promises = images.map(function (path) {

            this.http.get(URL, { headers: imgHeaders, search: params }).toPromise();
            // return http.__get({
            //     method: 'GET',
            //     url: this.routes.urls.APPLIANCE_HEALTH + '/' + path,
            //     responseType: 'blob',
            //     headers: {
            //       'Accept': 'image/png',
            //       'Auth-Token': Auth.getToken()
            //     }
            //  });
        });
        return Promise.all(promises);
    }
}
