import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { UserDashboardService } from '../user-dashboard.service';
import { DialogService } from '../../../core/services/dialog.service';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class SettingsComponent implements OnInit {

  resetForm: FormGroup;

    constructor(
        public apiService: UserDashboardService,
        public dialog: DialogService,
        public Toaster: ToastrService,
        private router: ActivatedRoute
    ) { }

    ngOnInit() {
        this.resetForm = new FormGroup({
            oldPassword: new FormControl('', [Validators.required]),
            newPassword: new FormControl('', [Validators.required]),
            reenterPassword: new FormControl('', [Validators.required, this.checkPasswordMatch.bind(this)])
        });
    }

    checkPasswordMatch(control: AbstractControl) {
        if (this.resetForm) {
            const password = this.resetForm.get('newPassword').value;
            return password !== control.value ? { notMatched: { value: control.value } } : null;
        }
        return null;
    }

    hasError(controlName: string, errorName: string) {
        return this.resetForm.controls[controlName].hasError(errorName);
    }

    changePassword(resetDetails, form) {
        const user = this.router.snapshot.paramMap.get('user') || null
        if (this.resetForm.valid) {
            const loader = this.dialog.createLoader();
            this.apiService.updatePassword(this.resetForm.value, user).then(() => {
                loader.close();
                this.Toaster.success('Password changed successfully');
                form.resetForm();
            }, () => {
                loader.close();
                this.dialog.createAlert('Unable change password. Please try later.');
            });
        }
    }
}
