import { Component, OnInit } from '@angular/core';
import { AdminDashboardService } from '../admin-dashboard.service';
import { DialogService } from '../../../core/services/dialog.service';
import { ToastrService } from 'ngx-toastr';
import { MatTableDataSource } from '@angular/material';

@Component({
    selector: 'app-manage-user-premiums',
    templateUrl: './manage-user-premiums.component.html',
    styleUrls: ['./manage-user-premiums.component.scss']
})
export class ManageUserPremiumsComponent implements OnInit {

    dataSource: any;
    clientLogin = {};

    displayedColumns: string[] = ['firstName', 'email', 'actions'];

    constructor(
        public apiSerive: AdminDashboardService,
        public dialog: DialogService,
        public toaster: ToastrService
    ) { }

    ngOnInit() {
        this.getClientsList();
    }

    getClientsList() {
        const loader = this.dialog.createLoader();
        this.apiSerive.getUsers()
            .then((response: any) => {
                this.dataSource = new MatTableDataSource(response.data.map(e => { e.action = true; return e }));
                loader.close();
            }, () => {
                this.dataSource = [];
                loader.close();
            });
    }

    applyFilter(filterValue: string) {
        this.dataSource.filter = filterValue.trim().toLowerCase();
    }

    activatePremium(element) {
        this.dialog.createConfirm(`Please confirm to active PREMIUM to ${element.firstName} ${element.lastName} (${element.email}).`, () => {
            const loader = this.dialog.createLoader();
            this.apiSerive.activatePremium(element.uuid)
                .then(() => {
                    loader.close();
                    element.premium = true;
                }, () => {
                    loader.close();
                    this.dialog.createAlert('Unable to activate. Please try later!');
                });
        });
    }

}
