import { Injectable } from '@angular/core';
import { HttpService } from '../../core/services/http.service';

@Injectable({
    providedIn: 'root'
})
export class AdminDashboardService {

    constructor(public http: HttpService) { }

    getClientsList() {
        return new Promise((resolve, reject) => {
            this.http.get('api/v1/clients', {}, {auth: true}).then((res) => {
                resolve(res.data);
            }, (err) => {
                reject(err);
            });
        });
    }

    updateClientLogin(client) {
        return this.http.put(`api/v1/clients/${client.uuid}`, client, {auth: true});
    }

    createClientLogin(client) {
        client.role = 'client';
        client.hearAboutUs = 'workingRelationship';
        return this.http.post('api/v1/user/register', client);
    }

    createJob(job) {
        return this.http.post('api/v1/jobs', job, {auth: true});
    }

    deleteClient(clientId) {
        return this.http.delete(`api/v1/clients/${clientId}`, {}, {auth: true});
    }

    getUsers() {
        return this.http.get('api/v1/users', {}, {auth: true});
    }

    activatePremium(id) {
        return this.http.put('api/v1/user/premium/'+id, {}, {auth: true});
    }

    uploadJobImage(image, id) {
        const formData = new FormData();
        if(image){
            formData.append('job', image, `job-${id}.jpg`);
        }
        return this.http.post('api/v1/jobs/image/'+id, formData, {auth: true, contentType: 'multipart/form-data'});
    }

    saveWorks(works) {
        return this.http.post('api/v1/works', works, {auth: true});
    }

    getWorks() {
        return new Promise((resolve, reject) => {
            this.http.get('api/v1/works', {}, {auth: true}).then((res) => {
                resolve(res.data);
            }, (err) => {
                reject(err);
            });
        });
    }

    deleteWork(work) {
        return new Promise((resolve, reject) => {
            this.http.delete('api/v1/works/' + work, {'work': work}, {auth: true}).then((res) => {
                resolve(res.data);
            }, (err) => {
                reject(err);
            });
        });
    }

}