import { Component, OnInit, Inject, ViewChild, ViewContainerRef, AfterViewInit, ComponentFactoryResolver } from '@angular/core';

import { MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { Subject } from 'rxjs';

@Component({
    selector: 'app-dialog',
    templateUrl: './dialog.component.html',
    styleUrls: ['./dialog.component.scss']
})
export class DialogComponent implements OnInit, AfterViewInit {
    
    componentRef: any;
    componentReady$ = new Subject<any>();
    onClose$ = new Subject<any>();

    @ViewChild('injectionContainer', {read: ViewContainerRef}) injectionContainer;

    constructor(
        private componentFactoryResolver: ComponentFactoryResolver,
        public dialogRef: MatDialogRef<DialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any
    ) { }

    ngOnInit() { }

    ngAfterViewInit() {
        if (this.data.component) {
            this.applyComponent(this.data.component);
        }
    }

    applyComponent(component: any): void {
        
        //grab component and load it in the dialog injection container
        const factory = this.componentFactoryResolver.resolveComponentFactory(component);
        this.componentRef = this.injectionContainer.createComponent(factory);

        //apply passed in component data to newly created component
        if (this.data.dataProperties) {
            const inputs = this.data.dataProperties.inputs;
            const outputs = this.data.dataProperties.outputs;
            if (inputs) {
                for(const input in inputs) {
                    this.componentRef.instance[input] = inputs[input];
                }
            }

            if (outputs) {
                for(const output in outputs) {
                    this.componentRef.instance[output].subscribe(outputs[output]);
                }
            }
        } else {    
            this.componentRef.instance.data = this.data.data;
        }

        //add dismiss method for dialog
        this.componentRef.instance.dismiss = this.close.bind(this);

        //set the dialog instance to the embedded component for reference
        this.componentRef.instance.dialogInstance = this;

        //notify any subscribers who are interested in the ready state of the dynamic component
        this.componentReady$.next(this.componentRef.instance);
    
    }

    close(): void {
        this.dialogRef.close();
    }

    confirm() {
        if(this.data.confirm) {
            this.data.confirm();
        }
        this.close();
    }

}
