import { Component, OnInit, Input } from '@angular/core';

@Component({
    selector: 'app-simple-table',
    templateUrl: './simple-table.component.html',
    styleUrls: ['./simple-table.component.scss']
})
export class SimpleTableComponent implements OnInit {
    @Input() lookup: any;
    @Input() data: any;
    @Input() widths: any;
    @Input() zerostate: any;
    @Input() key: string;
    @Input() ignoreEmpty = true;
    keys: any = [];
    ratio = {
        lg: 3,
        md: 4,
        sm: 6,
        xs: 6
    }

    constructor() { }

    ngOnInit() {
        this.data = this.data || {};
        if (this.lookup) {
            this.keys = Object.keys(this.lookup);
        } else {
            this.lookup = {};
            this.keys = Object.keys(this.data);
        }
        for(const key in this.data) {
            if (Array.isArray(this.data[key])) {
                this.data[key] = this.data[key].join(', ');
            }
        }
        this.ratio = {...this.ratio, ...(this.widths || {})};
    }

    ngOnChanges() {
        this.ngOnInit();
    }

    getCssClass(index: number) {
        if (this.keys.length - 1 === index) {
            return 'col';
        }
        return `col-lg-${this.ratio.lg} col-md-${this.ratio.md} col-sm-${this.ratio.sm} col-xs-${this.ratio.xs}`
    }

    hasData() {
        const values = Object.values(this.data || {});
        return !!(values.filter(x => !!x).length);
    }

    zerostateAction() {
        if (this.zerostate && this.zerostate.action) {
            this.zerostate.action(this.key);
        }
    }
}
