import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../core/services/api.service';
import { DialogService } from '../core/services/dialog.service';
import { TalentDetailsComponent } from '../talent-details/talent-details.component';
import { ToastrService } from 'ngx-toastr';
import { Options } from 'ng5-slider';
import { AuthService } from '../core/services/auth.service';

@Component({
    selector: 'app-talent',
    templateUrl: './talent.component.html',
    styleUrls: ['./talent.component.scss']
})
export class TalentComponent implements OnInit {

    zeroState = {
        message: 'Talent component is under construction'
    }
    profiles: any[];
    queryPayload: any = { nameOrId: null, ageTo: null, ageFrom: null, location: null, nationality: null, ethnicity: null, ethnicLook: null, eyeColor: null, hairColor: null, hairType: null, categories: null, languages: null };
    countries = [];
    gender = [
        { name: 'Male' },
        { name: 'Female' }
    ];
    appearance: any = {};
    eyeColor: any = {};
    hairColor: any = {};
    hairType: any = {};
    languages: any ={};
    categories = [];
    ageTo: any = {};
    ageFrom: any = {};
    ageRangeOptions: Options = {
        floor: 0,
        ceil: 100
    }
    isAdmin = false;
    activeFilter: any = null;

    get maxRangeMinValue() {
        return this.queryPayload['ageRange'].min + 1;
    }
    public parentCategory: Array<any> = [];

    public talentCount: Array<any> = [
        {
            category: "Acting & Extras",
            count: "46734",
            url: "#"
        },
        {
            category: "Entertaining",
            count: "28258",
            url: "#"
        },
        {
            category: "Promotion",
            count: "33345",
            url: "#"
        },
        {
            category: "Emcee & Voiceover",
            count: "15296",
            url: "#"
        },
        {
            category: "Modeling",
            count: "40426",
            url: "#"
        },
        {
            category: "TV Presenting",
            count: "18967",
            url: "#"
        }
    ];


    constructor(
        private router: Router,
        public apiService: ApiService,
        public auth: AuthService,
        public toaster: ToastrService,
        public dialog: DialogService
    ) {

    }

    ngOnInit() {
        this.ageTo = Array(100).fill(0).map((x,i)=>i+1)
        this.ageFrom = Array(100).fill(0).map((x,i)=>i+1)
        // this.queryPayload['ageTo'] = 100;
        this.queryPayload['ageFrom'] = 0;

        this.isAdmin = this.auth.getRole() === 'superAdmin';

        this.getTalents();
        const loader = this.dialog.createLoader();
        Promise.all([this.apiService.getCountries(), this.apiService.getEthnicity(), this.apiService.getCategories(),this.apiService.getParentCategories()]).then(async (res) => {
            loader.close();
            let parentCategories = [];
            this.countries = res[0].data;
            let appearance = JSON.parse(res[1].data['appearance']['ethnicLook']);
            let eyeColor = JSON.parse(res[1].data['appearance']['eyeColor']);
            let hairColor = JSON.parse(res[1].data['appearance']['hairColor']);
            let hairType = JSON.parse(res[1].data['appearance']['hairType']);
            this.languages = res[1].data['languages'];
            this.appearance = appearance;
            this.eyeColor = eyeColor;
            this.hairColor = hairColor;
            this.hairType = hairType;
            this.categories = res[2].data.map(e => e.category);
            res[3].data.map(async (category) => {
                let subValue = [];
                category.subCategory.map(subcategory => {
                    subValue.push(subcategory.category);
                });
                const payload = {
                    categories: subValue
                }
                let countCategory = await this.apiService.getProfilePerCategories(payload).then((data: any) => {
                    return data.length;
                });
                parentCategories.push({
                    id: category.id,
                    name: category.category,
                    value: subValue,
                    count: countCategory,
                    active: "notactive",
                })
            });
            this.parentCategory = parentCategories;

        }, () => {
            loader.close();
        });


    }

    onDataChange(filterType:string, $event: any){
        if($event.value){
            this.queryPayload[filterType] = $event.value;
        }
        else{
            this.queryPayload[filterType] = $event;
        }
    }

    async filterByCategory(filterdata, filterId){
        let active = "notactive";
        if(this.activeFilter){
            if(this.activeFilter == filterId){
                this.activeFilter = null
                active = "notactive";
                this.queryPayload['categories'] = null;
            }
            else{
                this.activeFilter = filterId;
                active = "active";
                this.queryPayload['categories'] = filterdata;
            }
        }
        else{
            this.activeFilter = filterId;
            active = "active";
            this.queryPayload['categories'] = filterdata;
        }

        this.parentCategory =  await this.parentCategory.map(item => {
            if (item.id === filterId) {
                return {
                    ...item,
                    active
                };

            }
            else{
                return {
                    ...item,
                    active: "notactive"
                };
            }
        });
        this.getTalents();
    }

    async getTalents() {
        const loader = this.dialog.createLoader();
        const payload = { ...this.queryPayload };

        this.apiService.getTalents(payload, this.isAdmin).then((res) => {
            loader.close();
            this.profiles = res.data;
        }, (res) => {
            loader.close();
            this.dialog.createAlert('Something went wrong!');
        });
    }

    async gettalentWithCategory(payload?: any){
        this.profiles = await this.apiService.getProfilePerCategories(payload).then((data: any) => {
            return data;
        });
    }

    getTalents1(changeIn?: any) {
        if (changeIn === 'min') {
            if (this.queryPayload['ageRange'].min >= this.queryPayload['ageRange'].max) {
                this.queryPayload['ageRange'].max += 10
            }
        }

        const loader = this.dialog.createLoader();
        const payload = { ...this.queryPayload };

        for (const k in this.queryPayload) {
            if (k === 'gender') {
                if (payload[k].length === 1 && payload[k][0] === "Kids") {
                    delete payload[k];
                    this.queryPayload.ageRange = { min: 0, max: 17 };
                    this.ageRangeOptions = { floor: 0, ceil: 17 };
                    payload['ageRange'] = `${this.queryPayload['ageRange'].min}-${this.queryPayload['ageRange'].max}`;
                } else {
                    this.ageRangeOptions = { floor: 0, ceil: 100 };
                    const index = payload[k].indexOf('Kids');
                    if (index !== -1) {
                        payload[k].splice(index, 1);
                    }
                }
            } else if (k === 'ageRange') {
                payload['ageRange'] = `${this.queryPayload['ageRange'].min}-${this.queryPayload['ageRange'].max}`;
            } else if (k === 'categories') {
                payload['categories'] = this.queryPayload['categories'].join(',');
                if (!payload['categories']) {
                    delete payload['categories'];
                }
            } else if (payload[k] === 'any') {
                delete payload[k];
            } else { }
        }

        this.apiService.getTalents(payload).then((res) => {
            loader.close();
            this.profiles = res.data;
        }, (res) => {
            loader.close();
            this.dialog.createAlert('Something went wrong!');
        });
    }

    onGenderChange() {
        this.queryPayload['gender'] = [];
        this.gender.forEach(g => {
            if (g['selected']) {
                this.queryPayload['gender'].push(g.name);
            }
        });
        this.getTalents();
    }

    async showDetails(event, talent) {
        if (event) {
            event.preventDefault();
            event.stopPropagation();
        }

        // [TODO] Should change this logic for talent profile navigation
        const dialogForm = await this.dialog.create({
            disableClose: true,
            height: '95vh',
            data: {
                type: 'custom',
                title: 'View Profile',
                component: TalentDetailsComponent,
                panelClass: 'custom',
                dataProperties: {
                    inputs: {
                        data: talent
                    }
                }
            }
        });
    }


}
