import {Injectable} from '@angular/core';
import {HttpService} from '../../core/services/http.service';
import {AuthService} from '../../core/services/auth.service';

@Injectable({
  providedIn: 'root'
})
export class UserDashboardService {

  constructor(public http: HttpService, public auth: AuthService) {
    console.log('I am user dash')
  }

  getProfileDetails(userId = null) {
    console.log(userId);
    let url = 'api/v1/user/profile';
    if (userId) {
      url = 'api/v1/user/' + userId + '/profile';
    }
    return this.http.get(url, {}, {auth: true});
  }

  updateProfileDetails(data, userId = null) {
    let url = 'api/v1/user/profile';
    if (userId) {
      url = 'api/v1/user/' + userId + '/profile';
    }
    let payload = {};
    const a = ['personalDetails', 'appearance', 'arts', 'sports', 'features', 'combatTraining', 'otherSkills', 'experience'];
    for (const key in data) {
      if (a.includes(key)) {
        if (key === 'experience') {
          payload['experience'] = data[key];
        } else {
          payload = {...payload, ...data[key]};
        }
      }
    }
    return this.http.put(url, payload, {auth: true});
  }

  uploadUserImage(image, userId) {
    let url = 'api/v1/user/image';
    if (userId) {
      url = 'api/v1/user/' + userId + '/image';
    }
    const payload = new FormData();
    payload.append('file', image, `img-${this.auth.getUserId()}`);
    return this.http.post(url, payload, {auth: true, contentType: 'multipart/form-data'});
  }

  toggleUserAproval(id): any {
    return this.http.put('api/v1/user/' + id + '/update-status', {}, {auth: true});
  }

  portfolio(payload, userId = null) {
    let url = 'api/v1/user/portfolio';
    if (userId) {
      url = 'api/v1/user/' + userId + '/portfolio';
    }
    return this.http.post(url, payload, {auth: true, contentType: 'multipart/form-data'});
  }

  getPortfolio(id) {
    let url = 'api/v1/user/portfolio';
    const header = {auth: true};
    if (id) {
      url += '/' + id;
      header.auth = false;
    }
    return this.http.get(url, {}, header);
  }

  getDropdownValues() {
    return this.http.get('api/v1/user/dropdown', {}, {auth: true});
  }

  updatePassword(payload, userId = null) {
    let url =  'api/v1/user/changepassword';
    if(userId) {
      url = 'api/v1/user/' + userId + 'changepassword';
    }
    return this.http.put(url, payload, {auth: true});
  }

  getPortfolioCounts(userId = null) {
    let url = 'api/v1/portfolio/counts';
    if (userId) {
      url = 'api/v1/portfolio/' + userId + '/counts'
    }

    if (this.auth.isAuthenticated()) {
      return this.http.get(url, {}, {auth: true});
    } else {
      return Promise.resolve({});
    }
  }

  deleteMedia(userId = null, payload) {
    let url = 'api/v1/user/portfolio/delete';
    if (userId) {
      url = 'api/v1/user/' + userId + '/portfolio/delete';
    }
    return this.http.post(url, payload, {auth: true});
  }
}
