import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-custom-form',
    templateUrl: './custom-form.component.html',
    styleUrls: ['./custom-form.component.scss']
})
export class CustomFormComponent implements OnInit {
    @Input() disableCancel = false;
    @Input() lookup: any;
    @Input() model: any;
    @Output() submit = new EventEmitter();

    formKeys: any;
    formGroup: FormGroup;

    constructor(public cd: ChangeDetectorRef) {
        cd.detach();
    }

    ngOnInit() {
        this.cd.reattach();
        const keys = Object.keys(this.lookup || {});
        const formControls =  {};
        keys.forEach(element => {
            if (this.lookup[element].type === 'range') {
                formControls[element] = new FormGroup({ min: new FormControl(
                    this.getValue(element).min,
                    this.lookup[element].validators
                ),
                max : new FormControl(
                    this.getValue(element).max,
                    this.lookup[element].validators
                )});
            } else {
                formControls[element] = new FormControl(
                    this.getValue(element),
                    this.lookup[element].validators
                );  
            }
        });
        this.formGroup = new FormGroup(formControls);
        this.formKeys = keys;
        this.cd.detectChanges();
    }

    getValue(field: string) {
        if (typeof this.model[field] !== 'undefined') {
            if (this.lookup[field].type === 'date') {
                return new Date(this.model[field]);
            } else if (this.lookup[field].type === 'dropdown') {
                if (this.lookup[field].multiple && typeof this.model[field] === 'string') {
                    return this.model[field].split(',').map(e => (e.trim()));
                } else {
                    return this.model[field];    
                }
            } else {
                return this.model[field];
            }
        } else if (this.lookup[field].type === 'range') {
            return {};
        }
        return null;
    }

    onSubmit(form) {
        // for(const k in form.value) {
        //     if (Array.isArray(form.value[k])) {
        //         form.value[k] = form.value[k].join(',');
        //     }
        // }
        this.submit.emit(form.value);
    }

    hasError(controlName: string, errorName: string) {
        return this.formGroup.controls[controlName].hasError(errorName);
    }

    onSelectChange(event, key) {
        if (this.lookup[key].change) {
            this.lookup[key].change(event);
        }
    }
}
