import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SignupComponent } from '../core/components/signup/signup.component';
import { LoginComponent } from '../core/components/login/login.component';
import { ResetPasswordComponent } from '../core/components/reset-password/reset-password.component';
import { AuthSuccessComponent } from '../core/components/auth-success/auth-success.component';
import { LandingComponent } from '../core/components/landing/landing.component';
import { AboutUsComponent } from '../about-us/about-us.component';
import { TalentComponent } from '../talent/talent.component';
import { JobsComponent } from '../jobs/jobs.component';
import { ContactComponent } from '../contact/contact.component';
import { MembershipComponent } from '../membership/membership.component';
import { TermsComponent } from '../terms/terms.component';
import { PrivacyComponent } from '../privacy/privacy.component';
import { DashboardComponent } from '../dashboard/dashboard.component';
import { GuardService } from '../core/services/guard.service';
import { TalentDetailsComponent } from '../talent-details/talent-details.component';
import { ZeroStateComponent } from '../core/components/zero-state/zero-state.component';
import { JobDetailsComponent } from '../jobs/job-details/job-details.component';
import { OurServicesComponent } from '../our-services/our-services.component';
import { FaqComponent } from '../faq/faq.component';
import { WorksComponent } from '../works/works.component';
import { RegistrationComponent } from '../registration/registration.component';
import {UserDashboardComponent} from '../dashboard/user-dashboard/user-dashboard.component';

const routes: Routes = [
  {
    path: 'signup',
    component: SignupComponent
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'reset-password',
    component: ResetPasswordComponent
  },
  {
    path: ':id/success',
    component: AuthSuccessComponent
  },
  {
    path: 'account-verification/:id',
    canActivate: [GuardService],
    component: ZeroStateComponent
  },
  {
    path: '',
    component: LandingComponent,
    children: [{
      path: 'works',
      component: WorksComponent
    }, {
      path: 'about',
      component: AboutUsComponent
    }, {
      path: 'services',
      component: OurServicesComponent
    }, {
      path: 'faq',
      component: FaqComponent
    }, {
      path: 'talent',
      component: TalentComponent
    }, {
      path: 'registration',
      component: RegistrationComponent
    }, {
      path: 'talent/:id',
      component: TalentDetailsComponent
    }, {
      path: 'jobs',
      component: JobsComponent
    }, {
      path: 'job/:id',
      component: JobDetailsComponent
    }, {
      path: 'contact',
      component: ContactComponent
    },
      {
        path: 'memberships',
        component: MembershipComponent
      },
      {
        path: 'terms',
        component: TermsComponent
      },
      {
        path: 'privacy',
        component: PrivacyComponent
      }, {
        path: 'dashboard',
        canActivate: [GuardService],
        canActivateChild: [GuardService],
        component: DashboardComponent
      },
      {
        path: 'dashboard/edit/:user/profile',
        canActivate: [GuardService],
        component: UserDashboardComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { onSameUrlNavigation: 'reload' })],
  exports: [RouterModule]
})
export class PagesRoutingModule { }
