import { Component, OnInit } from '@angular/core';
import { AdminDashboardService } from '../admin-dashboard.service';
import { DialogService } from '../../../core/services/dialog.service';
import { ToastrService } from 'ngx-toastr';
import { createWorkForm } from './work-form';
import { CustomFormComponent } from '../../../core/components/custom-form/custom-form.component';

@Component({
    selector: 'app-manage-works',
    templateUrl: './works.component.html',
    styleUrls: ['./works.component.scss']
})
export class ManageWorks implements OnInit {

    dataSource: any;
    clientLogin = {};
    commercials = [];
    films = [];

    
    constructor(
        public apiSerive: AdminDashboardService,
        public dialog: DialogService,
        public toaster: ToastrService
    ) { }

    ngOnInit() {
        this.getWorks();
    }

    getWorks() {
        const loader = this.dialog.createLoader();
        this.apiSerive.getWorks()
            .then((response: any) => {
                this.commercials = response.filter((item) => item.type === 'Commercial');
                this.films = response.filter((item) => item.type === 'Film');
                loader.close();
            }, () => {
                this.dataSource = [];
                loader.close();
            })
    }

    applyFilter(filterValue: string) {
        this.dataSource.filter = filterValue.trim().toLowerCase();
    }

    setWorks() {
        const form = {...createWorkForm};
        const dialogForm = this.dialog.createForm({
            type: 'custom',
            title: 'Add Works',
            component: CustomFormComponent,
            panelClass: 'custom',
            dataProperties: {
              inputs : {
                lookup: form,
                model: {}
            },
            outputs : {
                submit: (event) => {
                    const loader = this.dialog.createLoader();
                    
                    event.active = true;
                    this.apiSerive.saveWorks(event).then((res) => {
                        this.toaster.success(`works created successfully`);
                        loader.close();
                        dialogForm.close();
                        this.getWorks();
                    }, (err) => {
                        loader.close();
                        this.dialog.createAlert('Please try again later');
                    });
                }
            }
            }
        });
    }

    deleteWork(work) {
        this.dialog.createConfirm(`Are you sure? You want to delete this work`, () => {
            const loader = this.dialog.createLoader();
            this.apiSerive.deleteWork(work.id).then(() => {
                loader.close();
                this.toaster.success(`work deleted successfully`);
                this.getWorks();
            }, () => {
                loader.close();
                this.dialog.createAlert('Something went wrong. Please try again later.');
            })
        })
    }

}
