import { Injectable } from '@angular/core';
import { HttpService } from '../core/services/http.service';
import { AuthService } from '../core/services/auth.service';
const job = {
    title: 'Looking for pro swimmers for a sports shoot',
    description: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industrys standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap',
    location: {
        city: 'Dubai',
        country: 'UAE'
    },
    payment: true,
    callDate: '02/08/2019',
    expiresOn: '31/09/2019',
    availableRoles: 3
};


@Injectable({
    providedIn: 'root'
})
export class JobsService {
    constructor(public http: HttpService, public auth: AuthService) { }

    getAllJobs(queryParams = {}, payload = {}) {
        let queryString = ''
        for(const k in queryParams) {
            queryString = `${k}=${queryParams[k]}&`
        };

        const url = 'api/v1/getJobs' + (queryString ? ('?'+queryString+'t='+(new Date()).getTime()) : '');
        return new Promise((resolve, reject) => {
            this.http.post(url, payload).then((response) => {
                resolve(response.data);
            }, reject);
        });
    }

    getClientJobs() {
        return new Promise((resolve, reject) => {
            this.http.get('api/v1/client/jobs', {}, {auth: true}).then((response) => {
                resolve(response.jobs);
            }, reject);
        });
        
    }

    getUserJobs() {
        return new Promise((resolve, reject) => {
            this.http.get('api/v1/user/jobs', {}, {auth: true}).then((response) => {
                resolve(response.jobs);
            }, reject);
        });
    }

    getJobDetails(id) {
        return new Promise((resolve, reject) => {
            let url = `api/v1/jobs?id=${id}`;
            if (this.auth.isAuthenticated() && this.auth.getRole() === 'user') {
                url = `${url}&userId=${this.auth.getUserId()}`;
            }
            this.http.get(url).then((res) => {
                resolve(res.data[0]);
            }, reject);
        });
    }


    updateJob(job) {
        return this.http.put(`api/v1/jobs/${job.id}`, job, {auth: true});
    }

    deleteJob(jobId) {
        return this.http.delete(`api/v1/jobs/${jobId}`, {}, {auth: true});
    }

    updateRole(role) {
        return this.http.put(`api/v1/jobs/roles/${role.id}`, role, {auth: true});
    }

    deleteRole(roleId) {
        return this.http.delete(`api/v1/jobs/role/${roleId}`, {}, {auth: true});
    }

    applyJob(jobId, roleId) {
        return this.http.post('api/v1/job/application', {userId: this.auth._user, jobId: jobId, roleId: roleId}, {auth: true});
    }

    getAppliedUsers(payload) {
        return new Promise((resolve, reject) => {
            this.http.get('api/v1/job-applicants', payload, {auth: true}).then((response) => {
                resolve(response.jobs);
            }, reject);
        });
    }

    selectApplicants(payload) {
        return this.http.post('api/v1/job/application/selection', payload, {auth: true});
    }

    sendNotifications(payload) {
        return this.http.put('api/v1/job/application/notification', payload, {auth: true});
    }

    createRole(payload) {
        return this.http.post('api/v1/jobs/roles', payload, {auth: true});
    }

    getWorks() {
        return new Promise((resolve, reject) => {
            this.http.get('api/v1/works', {}).then((res) => {
                resolve(res.data);
            }, (err) => {
                reject(err);
            });
        });
    }
}
