                if (typeof res.data[form][key] === 'string') {
                  const range = res.data[form][key].split('-').map(e => Number(e));
                  const arr = [];
                  for (let i = range[0]; i <= range[1]; i++) {
                    arr.push({id: i, value: i});
                  }
                  ProfileForms[form][key].dropdownOptions = arr;
                } else {

                  if (key === 'languages') {
                    ProfileForms[form][key].dropdownOptions = (res.data[form][key]).map(e => ({id: e.name, value: e.name}));
                  } else {
                    ProfileForms[form][key].dropdownOptions = (res.data[form][key]).map(e => ({id: e, value: e}));
                  }
                }
