  openDialog(key = 'personalDetails', disableCancel = false) {
    let model = this.profile[key];
    if (key === 'experience') {
      model = null;
    }

    const dialogForm = this.dialog.createForm({
      type: 'custom',
      title: `EDIT ${this.dashboardTitles[key]}`,
      component: CustomFormComponent,
      panelClass: 'custom',
      dataProperties: {
        inputs: {
          disableCancel,
          lookup: ProfileForms[key],
          model: model || {}
        },
        outputs: {
          submit: (event) => {
            if (key === 'experience') {
              this.profileClone[key] = this.profileClone[key] || [];
              event['id'] = this.profileClone[key].length;
              this.profileClone[key].push(event);
            } else {
              this.profileClone[key] = event;
            }
            this.apiService.updateProfileDetails(this.profileClone, this.router.snapshot.paramMap.get('user'))
              .then(() => {
                this.toaster.success('Profile updated successfully');
                this.getProfileDetails(this.router.snapshot.paramMap.get('user'));
                dialogForm.close();
              });
          }
        }
      }
    });
  }

  editImage(disableCancel = false) {
    const context = this;
    const dialog = this.dialog.create({
      disableClose: true,
      data: {
        type: 'custom',
        title: 'Edit Profile Picture',
        component: EditProfilePicComponent,
        dataProperties: {
          inputs: {
            disableCancel
          },
          outputs: {
            afterUpload: function(event) {
              context.profile.personalDetails.image = event + '?t=' + (new Date()).getTime();
              context.auth._user.image = context.profile.personalDetails.image;
              dialog.close();
            }
          }
        }
      }
    });
  }

  addInformation(key: string) {
    this.openDialog(key);
  }

  deleteExperience(element) {
    const userId: any = this.router.snapshot.paramMap.get('user') || null;
    this.dialog.createConfirm('Are you sure want to delete experience?', () => {
      let expe = this.profileClone.experience;
      let len = this.profileClone.experience.length;

      for (let i = 0; i < len; i++) {
        if (element.id === expe[i].id) {
          expe.splice(i, 1);
          i--;
          len = expe.length;
        }
        if (i >= 0 && i < len) {

          expe[i].id = i;
        }
      }

      this.apiService.updateProfileDetails(this.profileClone, userId).then(() => {
        this.toaster.success('Experience deleted successfully');
        this.getProfileDetails(userId);
      });
    });
  }
